import BoxShadowControl from '../../components/box-shadow-control'
import TypographyControl from "../../components/typography"

import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'

import SPCAdvancedPanelBody from "../../components/advanced-panel-body"

import {
    TextControl,
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
    Button
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalButtonController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // container
        loadMoreProduct,
        gButtonAlignment,
        gButtonText,
        gButtonFontSize,
        gButtonFontSizeType,
        gButtonFontSizeTablet,
        gButtonFontSizeMobile,
        gButtonFontFamily,
        gButtonFontWeight,
        gButtonFontSubset,
        gButtonLineHeightType,
        gButtonLineHeight,
        gButtonLineHeightTablet,
        gButtonLineHeightMobile,
        gButtonLoadGoogleFonts,
        gButtonTextTransform,
        gButtonLetterSpacing,
        gButtonColor,
        gButtonBgColor,
        gButtonHcolor,
        gButtonBgHcolor,
        gButtonBorderStyle,
        gButtonBorderWidth,
        gButtonBorderColor,
        gButtonBorderHStyle,
        gButtonBorderHWidth,
        gButtonBorderHColor,
        gButtonSpaceing,
        gButtonPadding,
        gButtonPaddingUnit,
        gButtonBR,
        gButtonBRUnit,
        gButtonTopSpace,
        gButtonBottomSpace,
        gButtonMargin,
        gButtonMarginUnit,
        gButtonBoxShadowColor,
        gButtonBoxShadowHOffset,
        gButtonBoxShadowVOffset,
        gButtonBoxShadowBlur,
        gButtonBoxShadowSpread,
        gButtonBoxShadowPosition,
        gButtonBoxShadowHoverColor,
        gButtonBoxShadowHOHoverffset,
        gButtonBoxShadowVOHoverffset,
        gButtonBoxShadowHoverBlur,
        gButtonBoxShadowHoverSpread,
        gButtonBoxShadowHoverPosition,

    } = attributes;

    //margin start

    const [gButtonMarginControl, setgButtonMarginControl] = useState('individual');

    const gButtonMarginMin = (gButtonMarginUnit === 'em' || gButtonMarginUnit === 'rem' ? -12 : -200);
    const gButtonMarginMax = (gButtonMarginUnit === 'em' || gButtonMarginUnit === 'rem' ? 24 : 200);
    const gButtonMarginStep = (gButtonMarginUnit === 'em' || gButtonMarginUnit === 'rem' ? 0.1 : 1);

    const savegButtonMargin = (value) => {
        const newUpdate = gButtonMargin.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gButtonMargin: newUpdate,
        });
    };

    //margin end

    //padding start

    const [gButtonPaddingControl, setgButtonPaddingControl] = useState('individual');

    const gButtonPaddingMin = (gButtonPaddingUnit === 'em' || gButtonPaddingUnit === 'rem' ? -12 : -200);
    const gButtonPaddingMax = (gButtonPaddingUnit === 'em' || gButtonPaddingUnit === 'rem' ? 24 : 200);
    const gButtonPaddingStep = (gButtonPaddingUnit === 'em' || gButtonPaddingUnit === 'rem' ? 0.1 : 1);

    const savegButtonPadding = (value) => {
        const newUpdate = gButtonPadding.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gButtonPadding: newUpdate,
        });
    };

    //padding end

    //border radius start

    const [gButtonBRControl, setgButtonBRControl] = useState('individual');

    const gButtonBRMin = (gButtonBRUnit === 'em' || gButtonBRUnit === 'rem' ? -12 : -200);
    const gButtonBRMax = (gButtonBRUnit === 'em' || gButtonBRUnit === 'rem' ? 24 : 200);
    const gButtonBRStep = (gButtonBRUnit === 'em' || gButtonBRUnit === 'rem' ? 0.1 : 1);

    const savegButtonBR = (value) => {
        const newUpdate = gButtonBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gButtonBR: newUpdate,
        });
    };

    //border radius end

    return (

        <>

            {loadMoreProduct &&
                <SPCAdvancedPanelBody title={__("Load More button", 'shop-cred')} initialOpen={false}>

                    <Fragment>

                        <div className="spc-editor-alignment-group-wrapper">
                            <p> {__("Alignment", 'shop-cred')}</p>
                            <div className="spc-editor-alignment-group">
                                <Button
                                    className={'spc-editor-alignleft'}
                                    key={"left"}
                                    icon="editor-alignleft"
                                    label="Left"
                                    onClick={() => setAttributes({ gButtonAlignment: "left" })}
                                    aria-pressed={"left" === gButtonAlignment}
                                    isPrimary={"left" === gButtonAlignment}
                                />
                                <Button
                                    className={'spc-editor-aligncenter'}
                                    key={"center"}
                                    icon="editor-aligncenter"
                                    label="Center"
                                    onClick={() => setAttributes({ gButtonAlignment: "center" })}
                                    aria-pressed={"center" === gButtonAlignment}
                                    isPrimary={"center" === gButtonAlignment}
                                />
                                <Button
                                    className={'spc-editor-alignright'}
                                    key={"end"}
                                    icon="editor-alignright"
                                    label="Right"
                                    onClick={() => setAttributes({ gButtonAlignment: "end" })}
                                    aria-pressed={"end" === gButtonAlignment}
                                    isPrimary={"end" === gButtonAlignment}
                                />
                            </div>
                        </div>

                        <h2>{__("Button Text", 'shop-cred')}</h2>
                        <TextControl
                            label={__("Text", 'shop-cred')}
                            value={gButtonText}
                            onChange={value => setAttributes({ gButtonText: value })}
                        />

                        <Fragment>
                            <TypographyControl
                                label={__("Typography", 'shop-cred')}
                                attributes={attributes}
                                setAttributes={setAttributes}
                                loadGoogleFonts={{ value: gButtonLoadGoogleFonts, label: "gButtonLoadGoogleFonts" }}
                                fontFamily={{ value: gButtonFontFamily, label: "gButtonFontFamily" }}
                                fontWeight={{ value: gButtonFontWeight, label: "gButtonFontWeight" }}
                                fontSubset={{ value: gButtonFontSubset, label: "gButtonFontSubset" }}
                                fontSizeType={{ value: gButtonFontSizeType, label: "gButtonFontSizeType" }}
                                fontSize={{ value: gButtonFontSize, label: "gButtonFontSize" }}
                                fontSizeMobile={{ value: gButtonFontSizeMobile, label: "gButtonFontSizeMobile" }}
                                fontSizeTablet={{ value: gButtonFontSizeTablet, label: "gButtonFontSizeTablet" }}
                                lineHeightType={{ value: gButtonLineHeightType, label: "gButtonLineHeightType" }}
                                lineHeight={{ value: gButtonLineHeight, label: "gButtonLineHeight" }}
                                lineHeightMobile={{ value: gButtonLineHeightMobile, label: "gButtonLineHeightMobile" }}
                                lineHeightTablet={{ value: gButtonLineHeightTablet, label: "gButtonLineHeightTablet" }}
                                textTransform={{ value: gButtonTextTransform, label: 'gButtonTextTransform' }}
                                letterSpacing={{ value: gButtonLetterSpacing, label: 'gButtonLetterSpacing' }}
                            />

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Margin', 'shop-cred')}
                                    value={undefined !== gButtonMargin && undefined !== gButtonMargin[0] && undefined !== gButtonMargin[0].desk ? gButtonMargin[0].desk : ['20', '0', '0', '0']}
                                    control={gButtonMarginControl}
                                    tabletValue={undefined !== gButtonMargin && undefined !== gButtonMargin[0] && undefined !== gButtonMargin[0].tablet ? gButtonMargin[0].tablet : ['10', '0', '0', '0']}
                                    mobileValue={undefined !== gButtonMargin && undefined !== gButtonMargin[0] && undefined !== gButtonMargin[0].mobile ? gButtonMargin[0].mobile : ['10', '0', '0', '0']}
                                    onChange={(value) => savegButtonMargin({ desk: value })}
                                    onChangeTablet={(value) => savegButtonMargin({ tablet: value })}
                                    onChangeMobile={(value) => savegButtonMargin({ mobile: value })}
                                    onChangeControl={(value) => setgButtonMarginControl(value)}
                                    onChangeTabletControl={(value) => setgButtonMarginControl(value)}
                                    onChangeMobileControl={(value) => setgButtonMarginControl(value)}
                                    min={gButtonMarginMin}
                                    max={gButtonMarginMax}
                                    step={gButtonMarginStep}
                                    unit={gButtonMarginUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ gButtonMarginUnit: value })}
                                />
                            </PanelRow>

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Padding', 'shop-cred')}
                                    value={undefined !== gButtonPadding && undefined !== gButtonPadding[0] && undefined !== gButtonPadding[0].desk ? gButtonPadding[0].desk : ['8', '15', '8', '15']}
                                    control={gButtonPaddingControl}
                                    tabletValue={undefined !== gButtonPadding && undefined !== gButtonPadding[0] && undefined !== gButtonPadding[0].tablet ? gButtonPadding[0].tablet : ['8', '15', '8', '15']}
                                    mobileValue={undefined !== gButtonPadding && undefined !== gButtonPadding[0] && undefined !== gButtonPadding[0].mobile ? gButtonPadding[0].mobile : ['8', '15', '8', '15']}
                                    onChange={(value) => savegButtonPadding({ desk: value })}
                                    onChangeTablet={(value) => savgButtonPaddingn({ tablet: value })}
                                    onChangeMobile={(value) => savegButtonPadding({ mobile: value })}
                                    onChangeControl={(value) => setgButtonPaddingControl(value)}
                                    onChangeTabletControl={(value) => setgButtonPaddingControl(value)}
                                    onChangeMobileControl={(value) => setgButtonPaddingControl(value)}
                                    min={gButtonPaddingMin}
                                    max={gButtonPaddingMax}
                                    step={gButtonPaddingStep}
                                    unit={gButtonPaddingUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ gButtonPaddingUnit: value })}
                                />

                            </PanelRow>

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Border Radius', 'shop-cred')}
                                    value={undefined !== gButtonBR && undefined !== gButtonBR[0] && undefined !== gButtonBR[0].desk ? gButtonBR[0].desk : ['0', '0', '0', '0']}
                                    control={gButtonBRControl}
                                    tabletValue={undefined !== gButtonBR && undefined !== gButtonBR[0] && undefined !== gButtonBR[0].tablet ? gButtonBR[0].tablet : ['0', '0', '0', '0']}
                                    mobileValue={undefined !== gButtonBR && undefined !== gButtonBR[0] && undefined !== gButtonBR[0].mobile ? gButtonBR[0].mobile : ['0', '0', '0', '0']}
                                    onChange={(value) => savegButtonBR({ desk: value })}
                                    onChangeTablet={(value) => savegButtonBR({ tablet: value })}
                                    onChangeMobile={(value) => savegButtonBR({ mobile: value })}
                                    onChangeControl={(value) => setgButtonBRControl(value)}
                                    onChangeTabletControl={(value) => setgButtonBRControl(value)}
                                    onChangeMobileControl={(value) => setgButtonBRControl(value)}
                                    min={gButtonBRMin}
                                    max={gButtonBRMax}
                                    step={gButtonBRStep}
                                    unit={gButtonBRUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ gButtonBRUnit: value })}
                                />
                            </PanelRow>

                            <h2>{__("Button Colors", 'shop-cred')}</h2>
                            <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                                activeClass="active-tab"
                                tabs={[
                                    {
                                        name: "normal",
                                        title: __("Normal", 'shop-cred'),
                                        className: "spc-normal-tab spc-tabs-control",
                                    },
                                    {
                                        name: "hover",
                                        title: __("Hover", 'shop-cred'),
                                        className: "spc-focus-tab spc-tabs-control",
                                    },
                                ]}>
                                {
                                    (tabName) => {
                                        let tabout_color
                                        if ("normal" === tabName.name) {
                                            tabout_color = <Fragment>
                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Text Color", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gButtonColor}
                                                            onChange={(hexColor) => setAttributes({ gButtonColor: hexColor })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>
                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Background Color", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gButtonBgColor}
                                                            onChange={(colorValue) => setAttributes({ gButtonBgColor: colorValue })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>

                                                <PanelRow>
                                                    <SelectControl
                                                        label={__("Border Style", 'shop-cred')}
                                                        className="spc-select-control-wrapper"
                                                        value={gButtonBorderStyle}
                                                        options={[
                                                            { value: "none", label: __("None", 'shop-cred') },
                                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                            { value: "double", label: __("Double", 'shop-cred') },
                                                        ]}
                                                        onChange={value => {
                                                            setAttributes({ gButtonBorderStyle: value })
                                                        }}
                                                    />
                                                </PanelRow>
                                                {"none" !== gButtonBorderStyle &&
                                                    <PanelRow>
                                                        <div className="spc-full-range-wrapper">
                                                            <RangeControl
                                                                className='spc-range-control-wrapper'
                                                                label={__("Border Width (px)", 'shop-cred')}
                                                                value={gButtonBorderWidth}
                                                                onChange={value => {
                                                                    setAttributes({ gButtonBorderWidth: value })
                                                                }}
                                                                min={0}
                                                                max={20}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </PanelRow>
                                                }
                                                {"none" !== gButtonBorderStyle &&
                                                    <>

                                                        <div className="spc-color-component-settings">
                                                            <p className="spc-color-component-custom-text">
                                                                {__("Border Color", 'shop-cred')}
                                                            </p>
                                                            <ColorPalette
                                                                className="spc-color-component-custom-class"
                                                                value={gButtonBorderColor}
                                                                onChange={(value) => setAttributes({ gButtonBorderColor: value })}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </>
                                                }

                                                <h2>{__("Box Shadow", 'shop-cred')}</h2>

                                                <PanelRow>
                                                    <BoxShadowControl {...props}
                                                        attributes={attributes}
                                                        setAttributes={setAttributes}
                                                        label={__("Box Shadow", 'shop-cred')}
                                                        boxShadowColor={'gButtonBoxShadowColor'}
                                                        boxShadowHOffset={'gButtonBoxShadowHOffset'}
                                                        boxShadowVOffset={'gButtonBoxShadowVOffset'}
                                                        boxShadowBlur={'gButtonBoxShadowBlur'}
                                                        boxShadowSpread={'gButtonBoxShadowSpread'}
                                                        boxShadowPosition={'gButtonBoxShadowPosition'}
                                                    />
                                                </PanelRow>


                                            </Fragment>
                                        } else {
                                            tabout_color = <Fragment>
                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Text Hover", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gButtonHcolor}
                                                            onChange={(colorValue) => setAttributes({ gButtonHcolor: colorValue })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>

                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Background Hover", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gButtonBgHcolor}
                                                            onChange={(colorValue) => setAttributes({ gButtonBgHcolor: colorValue })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>

                                                <PanelRow>
                                                    <SelectControl
                                                        label={__("Border Style", 'shop-cred')}
                                                        className="spc-select-control-wrapper"
                                                        value={gButtonBorderHStyle}
                                                        options={[
                                                            { value: "none", label: __("None", 'shop-cred') },
                                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                            { value: "double", label: __("Double", 'shop-cred') },
                                                        ]}
                                                        onChange={value => {
                                                            setAttributes({ gButtonBorderHStyle: value })
                                                        }}
                                                    />
                                                </PanelRow>
                                                {"none" !== gButtonBorderHStyle &&
                                                    <PanelRow>
                                                        <div className="spc-full-range-wrapper">
                                                            <RangeControl
                                                                className='spc-range-control-wrapper'
                                                                label={__("Border Width (px)", 'shop-cred')}
                                                                value={gButtonBorderHWidth}
                                                                onChange={value => {
                                                                    setAttributes({ gButtonBorderHWidth: value })
                                                                }}
                                                                min={0}
                                                                max={20}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </PanelRow>
                                                }
                                                {"none" !== gButtonBorderHStyle &&
                                                    <>

                                                        <div className="spc-color-component-settings">
                                                            <p className="spc-color-component-custom-text">
                                                                {__("Border Color", 'shop-cred')}
                                                            </p>
                                                            <ColorPalette
                                                                className="spc-color-component-custom-class"
                                                                value={gButtonBorderHColor}
                                                                onChange={(value) => setAttributes({ gButtonBorderHColor: value })}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </>
                                                }

                                                <h2>{__("Box Shadow Hover", 'shop-cred')}</h2>

                                                <PanelRow>
                                                    <BoxShadowControl {...props}
                                                        attributes={attributes}
                                                        setAttributes={setAttributes}
                                                        label={__("Box Shadow", 'shop-cred')}
                                                        boxShadowColor={'gButtonBoxShadowHoverColor'}
                                                        boxShadowHOffset={'gButtonBoxShadowHOHoverffset'}
                                                        boxShadowVOffset={'gButtonBoxShadowVOHoverffset'}
                                                        boxShadowBlur={'gButtonBoxShadowHoverBlur'}
                                                        boxShadowSpread={'gButtonBoxShadowHoverSpread'}
                                                        boxShadowPosition={'gButtonBoxShadowHoverPosition'}
                                                    />
                                                </PanelRow>

                                            </Fragment>
                                        }
                                        return <div>{tabout_color}</div>
                                    }
                                }
                            </TabPanel>
                        </Fragment>

                    </Fragment>

                </SPCAdvancedPanelBody>
            }
        </>
    )
}

export default GlobalButtonController