// const { dateI18n, format, __experimentalGetSettings } = wp.date
const { decodeEntities } = wp.htmlEntities
import { __ } from '@wordpress/i18n';

export const ProductMeta = (props) => {

	const { post, attributes, categoriesList } = props

	var list = categoriesList;
	var cat = post.spc_product_taxonomy;
	var categoriesName = [];

	if (list !== undefined && cat !== undefined) {
		for (var j = 0; j < list.length; j++) {
			for (var i = 0; i < cat.length; i++) {
				if (list[j].id === cat[i]) {
					categoriesName.push(list[j].name);
				}
			}
		}
	}

	if (attributes.displayProductTaxonomy) {
		return (
			<div className='spc-product-cat'>

				{attributes.displayProductTaxonomy &&
					<span dangerouslySetInnerHTML={{ __html: cat }} className='spc-product-taxonomy' />
				}
			</div>
		)
	} else {
		return null
	}

}

