/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import FontFamilyControl from "./font-typography"
import RangeTypographyControl from "./range-typography"
import TextTransform from "./text-transform"
import LetterSpacing from "./letter-spacing"
import React, { useLayoutEffect } from 'react';
import { select } from '@wordpress/data'
import getSPCEditorStateLocalStorage from '../../utils/hooks/getSPCEditorStateLocalStorage';

import "./editor.scss"

const {
	Button,
} = wp.components

import {
	PanelRow
} from '@wordpress/components'
import icons from '../../utils/icons';


// Extend component
const { Fragment, useState } = wp.element


const TypographyControl = (props) => {

	const { getSelectedBlock } = select('core/block-editor');

	const [showAdvancedControls, setShowAdvancedControls] = useState(false);


	const activeClass = showAdvancedControls ? 'active' : '';

	const onAdvancedControlClick = () => {

		if (showAdvancedControls === false) {
			setShowAdvancedControls(true)
		} else {
			setShowAdvancedControls(false)
		}

	}

	useLayoutEffect(() => {
		window.addEventListener('click', function (e) {
			const popupButton = document.querySelector(`.spc-typography-options.active`);
			const popupWrap = document.querySelector(`.spc-typography-advanced.active`);

			if (popupButton && !popupButton?.contains(e.target) && popupWrap && !popupWrap?.contains(e.target) && !e.target?.classList?.contains('spc-responsive-tabs') && !e.target?.closest('.spc-responsive-tabs') && !e.target?.parentElement?.closest('.spc-typography-reset-btn')) {

				setShowAdvancedControls(false)

				const blockName = getSelectedBlock()?.name;
				const spcSettingState = getSPCEditorStateLocalStorage('spcSettingState');;

				const data = {
					...spcSettingState,
					[blockName]: {
						...spcSettingState?.[blockName],
						selectedSetting: false
					}
				}

				const spcLocalStorage = getSPCEditorStateLocalStorage();
				if (spcLocalStorage) {
					spcLocalStorage.setItem('spcSettingState', JSON.stringify(data));
				}
			}
		});
	}, []);


	const onAdvancedControlReset = () => {

		const { setAttributes } = props

		// Reset Font family to default.
		setAttributes({ [props.fontFamily.label]: "" })
		setAttributes({ [props.fontWeight.label]: "" })
		setAttributes({ [props.fontSubset.label]: "" })

		// Reset Font Size to default.
		setAttributes({ [props.fontSize.label]: "" })
		setAttributes({ [props.fontSizeType.label]: "px" })
		setAttributes({ [props.fontSizeMobile.label]: "" })
		setAttributes({ [props.fontSizeTablet.label]: "" })

		// Reset Line Height to default.
		setAttributes({ [props.lineHeight.label]: "" })
		setAttributes({ [props.lineHeightType.label]: "em" })
		setAttributes({ [props.lineHeightMobile.label]: "" })
		setAttributes({ [props.lineHeightTablet.label]: "" })

		// Reset Google Fonts to default.
		setAttributes({ [props.loadGoogleFonts.label]: false })

		setAttributes({ [props.textTransform.label]: 'none' })

		setAttributes({ [props.letterSpacing.label]: '0' })

	}



	let fontSize
	let fontWeight
	let fontFamily
	let fontAdvancedControls
	let fontTypoAdvancedControls
	let showAdvancedFontControls
	let resetFontAdvancedControls
	let textTransform
	let letterSpacing

	const {
		disableFontFamily,
		disableFontSize,
		disableLineHeight,
		disableTextTransform,
		disableLetterSpacing,
		disableAdvancedOptions = false
	} = props

	if (true !== disableFontFamily) {
		fontFamily = (
			<FontFamilyControl
				{...props}
			/>
		)
	}

	if (true !== disableTextTransform) {
		textTransform = (
			<TextTransform
				{...props}
			/>
		)
	}

	if (true !== disableLetterSpacing) {
		letterSpacing = (
			<LetterSpacing
				{...props}
			/>
		)
	}

	if (true !== disableLineHeight) {
		fontWeight = (
			<PanelRow>
				<RangeTypographyControl
					type={props.lineHeightType}
					typeLabel={props.lineHeightType.label}
					sizeMobile={props.lineHeightMobile}
					sizeMobileLabel={props.lineHeightMobile.label}
					sizeTablet={props.lineHeightTablet}
					sizeTabletLabel={props.lineHeightTablet.label}
					size={props.lineHeight}
					sizeLabel={props.lineHeight.label}
					sizeMobileText={__("Line Height", 'shop-cred')}
					sizeTabletText={__("Line Height", 'shop-cred')}
					sizeText={__("Line Height", 'shop-cred')}
					steps={0.1}
					{...props}
				/>
			</PanelRow>
		)
	}

	if (true !== disableFontSize) {
		fontSize = (
			<PanelRow>
				<RangeTypographyControl
					type={props.fontSizeType}
					typeLabel={props.fontSizeType.label}
					sizeMobile={props.fontSizeMobile}
					sizeMobileLabel={props.fontSizeMobile.label}
					sizeTablet={props.fontSizeTablet}
					sizeTabletLabel={props.fontSizeTablet.label}
					size={props.fontSize}
					sizeLabel={props.fontSize.label}
					sizeMobileText={(!props.fontSizeLabel) ? __("Font Size", 'shop-cred') : props.fontSizeLabel}
					sizeTabletText={(!props.fontSizeLabel) ? __("Font Size", 'shop-cred') : props.fontSizeLabel}
					sizeText={(!props.fontSizeLabel) ? __("Font Size", 'shop-cred') : props.fontSizeLabel}
					steps={1}
					{...props}
				/>
			</PanelRow>
		)
	}

	if (true !== disableFontFamily && true !== disableFontSize) {
		fontAdvancedControls = (
			<PanelRow>
				<Button
					className="spc-size-btn spc-typography-control-btn"
					isSmall
					aria-pressed={(showAdvancedControls !== null)}
					onClick={() => {

						const allPopups = document.querySelectorAll('.spc-typography-options');
						if (allPopups && 0 < allPopups.length) {
							for (let i = 0; i < allPopups.length; i++) {
								const popupButton = allPopups[i]?.querySelector('.spc-typography-options.active .spc-typography-control-btn');
								popupButton?.click();
							}
						}

						setShowAdvancedControls(!showAdvancedControls)

						const blockName = getSelectedBlock()?.name;
						const spcSettingState = getSPCEditorStateLocalStorage('spcSettingState');
						let data = {
							...spcSettingState,
							[blockName]: {
								...spcSettingState?.[blockName],
								selectedSetting: '.spc-typography-options'
							}
						}

						if (showAdvancedControls) {
							data = {
								...spcSettingState,
								[blockName]: {
									...spcSettingState?.[blockName],
									selectedSetting: false
								}
							}
						}

						const spcLocalStorage = getSPCEditorStateLocalStorage();
						if (spcLocalStorage) {
							spcLocalStorage.setItem('spcSettingState', JSON.stringify(data));
						}

					}
					}
				>{icons.text}</Button>
			</PanelRow>
		)

		resetFontAdvancedControls = (
			<PanelRow className={'spc-custom-row'}>
				<Button
					className="spc-size-btn spc-typography-reset-btn"
					isSmall
					aria-pressed={(showAdvancedControls !== null)}
					onClick={onAdvancedControlReset}
				>Reset</Button>
			</PanelRow>
		)
	} else {
		showAdvancedFontControls = (
			<Fragment>
				{fontSize}
				{fontFamily}
				{fontWeight}
				{textTransform}
				{letterSpacing}
			</Fragment>
		)
	}


	if (showAdvancedControls === true) {

		showAdvancedFontControls = (
			<div className={`spc-typography-advanced ${activeClass}`}>
				{fontSize}
				{fontFamily}
				{fontWeight}
				{textTransform}
				{letterSpacing}
			</div>
		)
	}

	if (true !== disableFontFamily && true !== disableFontSize) {
		fontTypoAdvancedControls = (
			<div className="spc-typography-option-actions">
				<span className="spc-typography-text">{props.label}</span>
				{fontAdvancedControls}
				{resetFontAdvancedControls}
			</div>
		)
	}

	return (
		<div className={`spc-typography-options ${activeClass}`}>
			{!disableAdvancedOptions &&
				<Fragment>
					{fontTypoAdvancedControls}
					{showAdvancedFontControls}
				</Fragment>
			}
		</div>
	)

}

export default TypographyControl
