/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

const {
	RangeControl,
	ButtonGroup,
	Button,
	Dashicon,
	PanelRow
} = wp.components

// Extend component
const { Fragment } = wp.element
const { useSelect, useDispatch } = wp.data;
import map from 'lodash/map';
import icons from '../../utils/icons';

/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function RangeTypographyControl(props) {
	const deviceType = useSelect((select) => {
		return select('core/edit-post').__experimentalGetPreviewDeviceType();
	}, []);
	const {
		__experimentalSetPreviewDeviceType: setPreviewDeviceType,
	} = useDispatch('core/edit-post');
	const customSetPreviewDeviceType = (device) => {
		setPreviewDeviceType(device);
	};
	const devices = [
		{
			name: 'Desktop',
			title: icons.desktop,
			itemClass: 'spc-desktop-tab spc-responsive-tabs',
		},
		{
			name: 'Tablet',
			title: icons.tablet,
			itemClass: 'spc-tablet-tab spc-responsive-tabs',
		},
		{
			name: 'Mobile',
			key: 'mobile',
			title: icons.mobile,
			itemClass: 'spc-mobile-tab spc-responsive-tabs',
		},
	];

	let sizeTypes

	if ("sizeTypes" in props) {
		sizeTypes = props.sizeTypes
	} else {
		sizeTypes = [
			{ key: "px", name: __("px", 'shop-cred') },
			{ key: "em", name: __("em", 'shop-cred') },
		]
	}

	const sizeTypesControls = (
		<div className="components-button-group components-spc-control-buttons__units">
			<ButtonGroup className="spc-size-type-field" aria-label={__("Size Type", 'shop-cred')}>
				{map(sizeTypes, ({ name, key }) => (
					<Button
						key={key}
						className="spc-size-btn"
						isSmall
						isPrimary={props.type.value === key}
						aria-pressed={props.type.value === key}
						onClick={() => props.setAttributes({ [props.typeLabel]: key })}
					>
						{name}
					</Button>
				))}
			</ButtonGroup>
		</div>
	)

	const output = {};
	output.Desktop = (
		<Fragment>
			{/* {sizeTypesControls} */}
			<PanelRow>
				<RangeControl className="spc-range-control spc-range-control-wrapper"
					label={__(props.sizeText)}
					value={props.size.value || ""}
					onChange={(value) => props.setAttributes({ [props.sizeLabel]: value })}
					min={0}
					max={100}
					step={props.steps}
					beforeIcon="editor-textcolor"
					// allowReset={true}
					initialPosition={30}
				/>
			</PanelRow>
		</Fragment>
	);
	output.Tablet = (
		<Fragment>
			{/* {sizeTypesControls} */}
			<PanelRow>
				<RangeControl className="spc-range-control spc-range-control-wrapper"
					label={__(props.sizeTabletText)}
					value={props.sizeTablet.value}
					onChange={(value) => props.setAttributes({ [props.sizeTabletLabel]: value })}
					min={0}
					max={100}
					step={props.steps}
					beforeIcon="editor-textcolor"
					// allowReset={true}
					initialPosition={30}
				/>
			</PanelRow>
		</Fragment>
	);
	output.Mobile = (
		<Fragment>
			{/* {sizeTypesControls} */}
			<PanelRow>
				<RangeControl className="spc-range-control spc-range-control-wrapper"
					label={__(props.sizeMobileText)}
					value={props.sizeMobile.value}
					onChange={(value) => props.setAttributes({ [props.sizeMobileLabel]: value })}
					min={0}
					max={100}
					step={props.steps}
					beforeIcon="editor-textcolor"
					// allowReset={true}
					initialPosition={30}
				/>
			</PanelRow>
		</Fragment>
	);

	return (
		<div className={'spc-typography-range-options'}>
			<div className="spc-size-type-field-tabs">
				<div className="spc-size-type-field-wrapper">
					{sizeTypesControls}
					<ButtonGroup className="spc-responsive-control-options components-tab-panel__tabs" aria-label={__('Device', 'shop-cred')}>
						{map(devices, ({ name, key, title, itemClass }) => (
							<Button
								key={key}
								className={`components-button components-tab-panel__tabs-item ${itemClass}${name === deviceType ? ' active-tab' : ''}`}
								aria-pressed={deviceType === name}
								onClick={() => customSetPreviewDeviceType(name)}
							>
								{title}
							</Button>
						))}
					</ButtonGroup>
				</div>
				<div className="spc-responsive-control-inner">
					{(output[deviceType] ? output[deviceType] : output.Desktop)}
				</div>
			</div>
		</div>
	);
}