const { dateI18n, format, __experimentalGetSettings } = wp.date

const { decodeEntities } = wp.htmlEntities

import { __ } from '@wordpress/i18n';

export const QuickView = (props) => {

	const {
		post,
		categoriesList,
		attributes
	} = props

	const dateFormat = __experimentalGetSettings().formats.date

	let quick_Text = (attributes.gQuickViewText) ? attributes.gQuickViewText : __("Quick View")

	var list = categoriesList;
	var cat = post.spc_product_taxonomy;
	var categoriesName = [];
	var p_id = post.spc_product_taxonomy

	if (list !== undefined && cat !== undefined) {
		for (var j = 0; j < list.length; j++) {
			for (var i = 0; i < cat.length; i++) {
				if (list[j].id === cat[i]) {
					categoriesName.push(list[j].name);
				}
			}
		}
	}

	if (attributes.displayQuickView) {
		return (
			<div className='quickview-wrapper'>

				{attributes.displayQuickView &&

					<a href="#" className='spc-product-quickview-btn spcquickview' >
						<span className="dashicons-search dashicons"></span>
						<span className='spc-quick-view' />

					</a>
				}
			</div>
		)
	} else {
		return null
	}

}

