import truncate from "lodash/truncate"

export const ProductExcerpt = (props) => {
	const { post, attributes } = props

	if (post.spc_excerpt == undefined) {
		return null
	}

	const words = post.spc_excerpt.split(" ")

	let excerpt = post.spc_excerpt

	let exLen = (attributes.productExcerptLength) ? attributes.productExcerptLength : 25

	if (exLen >= words.length) {
		excerpt = post.spc_excerpt
	} else {
		const truncated = words.slice(0, exLen)
		excerpt = truncated.join(" ")
		excerpt += " ..."
	}

	if (attributes.displayProductExcerpt && attributes.displayProductContentRadio == "excerpt") {

		return (

			<div className='spc-post-excerpt'>
				<p dangerouslySetInnerHTML={{ __html: excerpt }} />
			</div>
		)

	} else if (attributes.displayProductExcerpt && attributes.displayProductContentRadio == "full_post") {

		return (
			<div className='spc-post-excerpt spc-full_postt'>

				<p dangerouslySetInnerHTML={{ __html: post.content.raw.trim() }} />

			</div>
		)


	} else {
		return null
	}
}


