/**
 * Box-Shadow reusable component.
 *
 */
import { __ } from '@wordpress/i18n';
import './editor.scss';
import icons from '../../utils/icons';
import '../color/editor.scss'
import '../range-control/editor.scss'

const {
    ColorPalette
} = wp.blockEditor

const {
    Button,
    SelectControl,
    RangeControl
} = wp.components

// Extend component
const { Fragment, useState } = wp.element

const BoxShadowControl = (props) => {

    const [showAdvancedControls, setShowAdvancedControls] = useState(false);


    const onAdvancedControlClick = () => {

        if (showAdvancedControls === false) {
            setShowAdvancedControls(true)
        } else {
            setShowAdvancedControls(false)
        }
    }

    const onAdvancedControlReset = () => {

        const { setAttributes } = props

        setAttributes({ boxShadowColor: "" })
        setAttributes({ boxShadowHOffset: "" })
        setAttributes({ boxShadowVOffset: "" })
        setAttributes({ boxShadowBlur: "" })
        setAttributes({ boxShadowSpread: "" })
        setAttributes({ boxShadowPosition: "" })
    }

    const {
        attributes,
        setAttributes,
        boxShadowColor,
        boxShadowHOffset,
        boxShadowVOffset,
        boxShadowBlur,
        boxShadowSpread,
        boxShadowPosition
    } = props

    let advancedControls

    if (showAdvancedControls === true) {
        advancedControls = (
            <div className="spc-box-shadow-advanced">
                <Fragment>
                    <div className="spc-color-component-settings">
                        <p className="spc-color-component-custom-text">
                            {__("Color", 'shop-cred')}
                        </p>
                        <ColorPalette
                            className="spc-color-component-custom-class"
                            value={attributes[boxShadowColor]}
                            onChange={(value) => setAttributes({ [boxShadowColor]: value })}
                            allowReset
                            enableAlpha
                        />
                    </div>
                    <RangeControl className="spc-range-control-wrapper spc-range-control"
                        label={__("Horizontal", 'shop-cred')}
                        value={attributes[boxShadowHOffset]}
                        onChange={(value) => setAttributes({ [boxShadowHOffset]: value })}
                        min={-100}
                        max={100}
                        // allowReset
                        initialPosition={0}
                    />
                    <RangeControl
                        className="spc-range-control-wrapper spc-range-control"
                        label={__("Vertical", 'shop-cred')}
                        value={attributes[boxShadowVOffset]}
                        onChange={(value) => setAttributes({ [boxShadowVOffset]: value })}
                        min={-100}
                        max={100}
                        // allowReset
                        initialPosition={0}
                    />
                    <RangeControl
                        className="spc-range-control-wrapper spc-range-control"
                        label={__("Blur", 'shop-cred')}
                        value={attributes[boxShadowBlur]}
                        onChange={(value) => setAttributes({ [boxShadowBlur]: value })}
                        min={0}
                        max={100}
                        // allowReset
                        initialPosition={0}
                    />
                    <RangeControl
                        className="spc-range-control-wrapper spc-range-control"
                        label={__("Spread", 'shop-cred')}
                        value={attributes[boxShadowSpread]}
                        onChange={(value) => setAttributes({ [boxShadowSpread]: value })}
                        min={0}
                        max={100}
                        // allowReset
                        initialPosition={0}
                    />
                    <SelectControl
                        label={__('Position', 'shop-cred')}
                        className="spc-select-control-wrapper"
                        value={attributes[boxShadowPosition] ? attributes[boxShadowPosition] : 'outset'}
                        onChange={(value) => setAttributes({ [boxShadowPosition]: value })}
                        options={[
                            { value: "outset", label: __("Outset", 'shop-cred') },
                            { value: "inset", label: __("Inset", 'shop-cred') },
                        ]}
                    />
                </Fragment>
            </div>
        );
    }

    const resetBoxShadowAdvancedControls = (
        <Button
            className="spc-size-btn spc-typography-reset-btn"
            isSmall
            aria-pressed={(showAdvancedControls !== null)}
            onClick={onAdvancedControlReset}
        >Reset
        </Button>
    );

    const boxShadowAdvancedControls = (
        <Button
            className="spc-size-btn spc-typography-control-btn spc-box-shadow-control-btn"
            isSmall
            aria-pressed={(showAdvancedControls !== null)}
            onClick={onAdvancedControlClick}
        >{icons.edit}
        </Button>
    );

    return (
        <div className='spc-box-shadow-wrapper'>
            <span>{props.label}</span>
            <div className='spc-box-shadow-control-wrapper'>
                {boxShadowAdvancedControls}
                {resetBoxShadowAdvancedControls}
                {advancedControls}
            </div>
        </div>
    )
}

export default BoxShadowControl;