const { decodeEntities } = wp.htmlEntities

import { __ } from '@wordpress/i18n';

export const ProductImage = (props) => {

	const { post, attributes } = props

	let target = (attributes.newTab) ? "_blank" : "_self"

	var src = post.spc_featured_image_src[attributes.imgSize]

	if (
		attributes.displayProductImage &&
		undefined !== post.spc_featured_image_src &&
		attributes.imgSize &&
		post.spc_featured_image_src[attributes.imgSize]
	) {

		var src = post.spc_featured_image_src[attributes.imgSize]

		return (
			<div className='spc-product-image'>

				<a className="product-img" href={post.link} target={target} rel="noopener noreferrer">
					<img
						src={src[0]}
						alt={decodeEntities(post.title.rendered.trim()) || __("(Untitled)")}
					/>
				</a>
				<div className='quickview-wrapper'>

					{attributes.displayQuickView &&

						<a href="#" className='spc-product-quickview-btn spcquickview' >
							<span className="spm-search-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-search"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg></span>
							<span className='spc-quick-view' />
						</a>
					}
				</div>

			</div>
		)
	} else {

		return null

	}

}
