import SPCAdvancedPanelBody from "../../components/advanced-panel-body"
import BoxShadowControl from '../../components/box-shadow-control'
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'

import {
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
} from '@wordpress/components';

import {
    Fragment,
    useState
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalSwiperDotsController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // Swiper Dots
        productCarouselPagination,
        gSwiperDotsColor,
        gSwiperDotsHColor,
        gSwiperDotsBGColor,
        gSwiperDotsBGHColor,
        gSwiperDotsBR,
        gSwiperDotsBRUnit,
        gSwiperDotsBRH,
        gSwiperDotsBRHUnit,
        gSwiperDotsBorderStyleNormal,
        gSwiperDotsBorderWidthNormal,
        gSwiperDotsBorderColorNormal,
        gSwiperDotsBorderStyleHover,
        gSwiperDotsBorderWidthHover,
        gSwiperDotsBorderColorHover,
        gSwiperDotsShadowColor,
        gSwiperDotsShadowHOffset,
        gSwiperDotsShadowVOffset,
        gSwiperDotsShadowBlur,
        gSwiperDotsShadowSpread,
        gSwiperDotsShadowPosition,
        gSwiperDotsShadowColorHover,
        gSwiperDotsShadowHOffsetHover,
        gSwiperDotsShadowVOffsetHover,
        gSwiperDotsShadowBlurHover,
        gSwiperDotsShadowSpreadHover,
        gSwiperDotsShadowPositionHover

    } = attributes;


    //border radius start

    const [gSwiperDotsBRControl, setgSwiperDotsBRControl] = useState('individual');

    const gSwiperDotsBRMin = (gSwiperDotsBRUnit === 'em' || gSwiperDotsBRUnit === 'rem' ? -12 : -200);
    const gSwiperDotsBRMax = (gSwiperDotsBRUnit === 'em' || gSwiperDotsBRUnit === 'rem' ? 24 : 200);
    const gSwiperDotsBRStep = (gSwiperDotsBRUnit === 'em' || gSwiperDotsBRUnit === 'rem' ? 0.1 : 1);

    const savegSwiperDotsBR = (value) => {
        const newUpdate = gSwiperDotsBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gSwiperDotsBR: newUpdate,
        });
    };

    //border radius end

    //border radius ACTIVE start

    const [gSwiperDotsBRHControl, setgSwiperDotsBRHControl] = useState('individual');

    const gSwiperDotsBRHMin = (gSwiperDotsBRHUnit === 'em' || gSwiperDotsBRHUnit === 'rem' ? -12 : -200);
    const gSwiperDotsBRHMax = (gSwiperDotsBRHUnit === 'em' || gSwiperDotsBRHUnit === 'rem' ? 24 : 200);
    const gSwiperDotsBRHStep = (gSwiperDotsBRHUnit === 'em' || gSwiperDotsBRHUnit === 'rem' ? 0.1 : 1);

    const savegSwiperDotsBRH = (value) => {
        const newUpdate = gSwiperDotsBRH.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gSwiperDotsBRH: newUpdate,
        });
    };
    //border radius ACTIVE end


    return (

        <>

            <SPCAdvancedPanelBody title={__("Dots", 'shop-cred')} initialOpen={false}>

                <hr className="spc-editor-separator" />

                <PanelRow className="spc-relative-row">
                    <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                        activeClass="active-tab"
                        tabs={[
                            {
                                name: "normal",
                                title: __("Normal", 'shop-cred'),
                                className: "spc-normal-tab spc-tabs-control",
                            },
                            {
                                name: "hover",
                                title: __("Hover", 'shop-cred'),
                                className: "spc-focus-tab spc-tabs-control",
                            },
                        ]}>
                        {
                            (tabName) => {
                                let tabout_color
                                if ("normal" === tabName.name) {
                                    tabout_color = <Fragment>

                                        <>
                                            <hr className="spc-editor-separator" />
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Normal", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperDotsColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperDotsColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>

                                        <>
                                            <hr className="spc-editor-separator" />
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Background Hover", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperDotsBGColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperDotsBGColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <PanelRow>
                                            <SelectControl
                                                label={__("Border Style", 'shop-cred')}
                                                className="spc-select-control-wrapper"
                                                value={gSwiperDotsBorderStyleNormal}
                                                options={[
                                                    { value: "none", label: __("None", 'shop-cred') },
                                                    { value: "solid", label: __("Solid", 'shop-cred') },
                                                    { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                    { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                    { value: "double", label: __("Double", 'shop-cred') },
                                                ]}
                                                onChange={value => {
                                                    setAttributes({ gSwiperDotsBorderStyleNormal: value })
                                                }}
                                            />
                                        </PanelRow>
                                        {"none" !== gSwiperDotsBorderStyleNormal &&
                                            <PanelRow>
                                                <div className="spc-full-range-wrapper">
                                                    <RangeControl
                                                        className='spc-range-control-wrapper'
                                                        label={__("Border Width (px)", 'shop-cred')}
                                                        value={gSwiperDotsBorderWidthNormal}
                                                        onChange={value => {
                                                            setAttributes({ gSwiperDotsBorderWidthNormal: value })
                                                        }}
                                                        min={0}
                                                        max={20}
                                                        allowReset
                                                    />
                                                </div>
                                            </PanelRow>
                                        }
                                        {"none" !== gSwiperDotsBorderStyleNormal &&
                                            <>
                                                <hr className="spc-editor-separator" />
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Border Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gSwiperDotsBorderColorNormal}
                                                        onChange={(value) => setAttributes({ gSwiperDotsBorderColorNormal: value })}
                                                        allowReset
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                        }
                                        <PanelRow className="spc-relative-row">
                                            <ResponsiveMeasurementControls
                                                label={__('Border Radius', 'shop-cred')}
                                                value={undefined !== gSwiperDotsBR && undefined !== gSwiperDotsBR[0] && undefined !== gSwiperDotsBR[0].desk ? gSwiperDotsBR[0].desk : ['50', '50', '50', '50']}
                                                control={gSwiperDotsBRControl}
                                                tabletValue={undefined !== gSwiperDotsBR && undefined !== gSwiperDotsBR[0] && undefined !== gSwiperDotsBR[0].tablet ? gSwiperDotsBR[0].tablet : ['50', '50', '50', '50']}
                                                mobileValue={undefined !== gSwiperDotsBR && undefined !== gSwiperDotsBR[0] && undefined !== gSwiperDotsBR[0].mobile ? gSwiperDotsBR[0].mobile : ['50', '50', '50', '50']}
                                                onChange={(value) => savegSwiperDotsBR({ desk: value })}
                                                onChangeTablet={(value) => savegSwiperDotsBR({ tablet: value })}
                                                onChangeMobile={(value) => savegSwiperDotsBR({ mobile: value })}
                                                onChangeControl={(value) => setgSwiperDotsBRControl(value)}
                                                onChangeTabletControl={(value) => setgSwiperDotsBRControl(value)}
                                                onChangeMobileControl={(value) => setgSwiperDotsBRControl(value)}
                                                min={gSwiperDotsBRMin}
                                                max={gSwiperDotsBRMax}
                                                step={gSwiperDotsBRStep}
                                                unit={gSwiperDotsBRUnit}
                                                units={['px', 'em', '%']}
                                                onUnit={(value) => setAttributes({ gSwiperDotsBRUnit: value })}
                                            />
                                        </PanelRow>

                                        <PanelRow>
                                            <BoxShadowControl {...props}
                                                attributes={attributes}
                                                setAttributes={setAttributes}
                                                label={__("Box Shadow", 'shop-cred')}
                                                boxShadowColor={'gSwiperDotsShadowColor'}
                                                boxShadowHOffset={'gSwiperDotsShadowHOffset'}
                                                boxShadowVOffset={'gSwiperDotsShadowVOffset'}
                                                boxShadowBlur={'gSwiperDotsShadowBlur'}
                                                boxShadowSpread={'gSwiperDotsShadowSpread'}
                                                boxShadowPosition={'gSwiperDotsShadowPosition'}
                                            />
                                        </PanelRow>
                                    </Fragment>
                                } else {
                                    tabout_color = <Fragment>

                                        <>
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Hover Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperDotsHColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperDotsHColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <>
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Background Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperDotsBGHColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperDotsBGHColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <PanelRow>
                                            <SelectControl
                                                label={__("Border Style", 'shop-cred')}
                                                className="spc-select-control-wrapper"
                                                value={gSwiperDotsBorderStyleHover}
                                                options={[
                                                    { value: "none", label: __("None", 'shop-cred') },
                                                    { value: "solid", label: __("Solid", 'shop-cred') },
                                                    { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                    { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                    { value: "double", label: __("Double", 'shop-cred') },
                                                ]}
                                                onChange={value => {
                                                    setAttributes({ gSwiperDotsBorderStyleHover: value })
                                                }}
                                            />
                                        </PanelRow>
                                        {"none" !== gSwiperDotsBorderStyleHover &&
                                            <PanelRow>
                                                <div className="spc-full-range-wrapper">
                                                    <RangeControl
                                                        className='spc-range-control-wrapper'
                                                        label={__("Border Width (px)", 'shop-cred')}
                                                        value={gSwiperDotsBorderWidthHover}
                                                        onChange={value => {
                                                            setAttributes({ gSwiperDotsBorderWidthHover: value })
                                                        }}
                                                        min={0}
                                                        max={20}
                                                        allowReset
                                                    />
                                                </div>
                                            </PanelRow>
                                        }
                                        {"none" !== gSwiperDotsBorderStyleHover &&
                                            <>
                                                <hr className="spc-editor-separator" />
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Border Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gSwiperDotsBorderColorHover}
                                                        onChange={(value) => setAttributes({ gSwiperDotsBorderColorHover: value })}
                                                        allowReset
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                        }

                                        <PanelRow className="spc-relative-row">
                                            <ResponsiveMeasurementControls
                                                label={__('Border Radius', 'shop-cred')}
                                                value={undefined !== gSwiperDotsBRH && undefined !== gSwiperDotsBRH[0] && undefined !== gSwiperDotsBRH[0].desk ? gSwiperDotsBRH[0].desk : ['50', '50', '50', '50']}
                                                control={gSwiperDotsBRHControl}
                                                tabletValue={undefined !== gSwiperDotsBRH && undefined !== gSwiperDotsBRH[0] && undefined !== gSwiperDotsBRH[0].tablet ? gSwiperDotsBRH[0].tablet : ['50', '50', '50', '50']}
                                                mobileValue={undefined !== gSwiperDotsBRH && undefined !== gSwiperDotsBRH[0] && undefined !== gSwiperDotsBRH[0].mobile ? gSwiperDotsBRH[0].mobile : ['50', '50', '50', '50']}
                                                onChange={(value) => savegSwiperDotsBRH({ desk: value })}
                                                onChangeTablet={(value) => savegSwiperDotsBRH({ tablet: value })}
                                                onChangeMobile={(value) => savegSwiperDotsBRH({ mobile: value })}
                                                onChangeControl={(value) => setgSwiperDotsBRHControl(value)}
                                                onChangeTabletControl={(value) => setgSwiperDotsBRHControl(value)}
                                                onChangeMobileControl={(value) => setgSwiperDotsBRHControl(value)}
                                                min={gSwiperDotsBRHMin}
                                                max={gSwiperDotsBRHMax}
                                                step={gSwiperDotsBRHStep}
                                                unit={gSwiperDotsBRHUnit}
                                                units={['px', 'em', '%']}
                                                onUnit={(value) => setAttributes({ gSwiperDotsBRHUnit: value })}
                                            />
                                        </PanelRow>
                                        <PanelRow>
                                            <BoxShadowControl {...props}
                                                attributes={attributes}
                                                setAttributes={setAttributes}
                                                label={__("Box Shadow", 'shop-cred')}
                                                boxShadowColor={'gSwiperDotsShadowColorHover'}
                                                boxShadowHOffset={'gSwiperDotsShadowHOffsetHover'}
                                                boxShadowVOffset={'gSwiperDotsShadowVOffsetHover'}
                                                boxShadowBlur={'gSwiperDotsShadowBlurHover'}
                                                boxShadowSpread={'gSwiperDotsShadowSpreadHover'}
                                                boxShadowPosition={'gSwiperDotsShadowPositionHover'}
                                            />
                                        </PanelRow>
                                    </Fragment>
                                }
                                return <div>{tabout_color}</div>
                            }
                        }
                    </TabPanel>
                </PanelRow>
            </SPCAdvancedPanelBody>

        </>
    )
}

export default GlobalSwiperDotsController