/**
 * Responsive Range Component
 *
 */
/**
 * Internal block libraries
 */
import { useSelect, useDispatch } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { map, isEqual } from 'lodash';
import { undo } from '@wordpress/icons';
import RangeControl from '../range-control';
import icons from '../../../utils/icons';

import {
	Dashicon,
	Button,
	ButtonGroup,
} from '@wordpress/components';

/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function ResponsiveRangeControls({
	label,
	onChange,
	onChangeTablet,
	onChangeMobile,
	mobileValue,
	tabletValue,
	value,
	step = 1,
	max = 100,
	min = 0,
	unit = '',
	onUnit,
	showUnit = false,
	units = ['px', 'em', 'rem'],
	allowEmpty = true,
	className = '',
	reset,
}) {
	const [deviceType, setDeviceType] = useState('Desktop');
	const theDevice = useSelect((select) => {
		return select('ShopCredBlocks/data').getPreviewDeviceType();
	}, []);
	if (theDevice !== deviceType) {
		setDeviceType(theDevice);
	}
	const {
		setPreviewDeviceType,
	} = useDispatch('ShopCredBlocks/data');
	const customSetPreviewDeviceType = (device) => {
		setPreviewDeviceType(device);
		setDeviceType(device);
	};
	const devices = [
		{
			name: 'Desktop',
			key: 'desktop',
			title: icons.desktop,
			itemClass: 'spc-desk-tab',
		},
		{
			name: 'Tablet',
			key: 'tablet',
			title: icons.tablet,
			itemClass: 'spc-tablet-tab',
		},
		{
			name: 'Mobile',
			key: 'mobile',
			title: icons.mobile,
			itemClass: 'spc-mobile-tab',
		},
	];
	const output = {};
	output.Mobile = (
		<div className="spc-full-range-wrapper">
			<RangeControl
				key={2}
				className='spc-range-control-wrapper'
				value={(undefined !== mobileValue ? mobileValue : '')}
				onChange={(size) => onChangeMobile(size)}
				min={min}
				max={max}
				step={step}
				unit={unit}
				onUnit={onUnit}
				showUnit={showUnit}
				units={units}
			/>
		</div>
	);
	output.Tablet = (
		<div className="spc-full-range-wrapper">
			<RangeControl
				key={1}
				className='spc-range-control-wrapper'
				value={(undefined !== tabletValue ? tabletValue : '')}
				onChange={(size) => onChangeTablet(size)}
				min={min}
				max={max}
				step={step}
				unit={unit}
				onUnit={onUnit}
				showUnit={showUnit}
				units={units}
			/>
		</div>
	);
	output.Desktop = (
		<div className="spc-full-range-wrapper">
			<RangeControl
				key={0}
				className='spc-range-control-wrapper'
				value={(undefined !== value ? value : '')}
				onChange={(size) => onChange(size)}
				min={min}
				max={max}
				step={step}
				unit={unit}
				onUnit={onUnit}
				showUnit={showUnit}
				units={units}
			/>
		</div>
	);
	return [
		onChange && onChangeTablet && onChangeMobile && (
			<div className={`components-base-control spc-responsive-range-control${'' !== className ? ' ' + className : ''}`}>
				<div className="spc-title-bar">
					{reset && (
						<Button
							className="is-reset is-single"
							isSmall
							disabled={((isEqual('', value)) ? true : false)}
							icon={undo}
							onClick={() => reset()}
						></Button>
					)}
					{label && (
						<span className="spc-control-title">{label}</span>
					)}
					<ButtonGroup className="spc-measure-responsive-options" aria-label={__('Device', 'shop-cred')}>
						{map(devices, ({ name, key, title, itemClass }) => (
							<Button
								key={key}
								className={`spc-responsive-btn ${itemClass}${name === deviceType ? ' is-active' : ''}`}
								isSmall
								aria-pressed={deviceType === name}
								onClick={() => customSetPreviewDeviceType(name)}
							>
								{title}
							</Button>
						))}
					</ButtonGroup>
				</div>
				{(output[deviceType] ? output[deviceType] : output.Desktop)}
			</div>
		),
	];
}
