const { decodeEntities } = wp.htmlEntities
import "./editor.scss"
import { __ } from '@wordpress/i18n';

export const ProductRating = (props) => {

	const { attributes, post } = props

	if (undefined == post.spc_product_rating) {
		return null
	}

	if (attributes.displayProductRating && post.spc_product_rating) {
		return (

			<div dangerouslySetInnerHTML={{ __html: post.spc_product_rating }} className='spc-product-rating woocommerce-product-rating' />
		)
	} else {
		return null
	}

}