/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

const {
	ButtonGroup,
	Button,
	Dashicon,
} = wp.components

// Extend component
const { Fragment } = wp.element
const { useSelect, useDispatch } = wp.data;
import map from 'lodash/map';

/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function ResponsiveControl(props) {
	const deviceType = useSelect((select) => {
		// return select( 'core/edit-post' ).__experimentalGetPreviewDeviceType();
		return select('ShopCredBlocks/data').getPreviewDeviceType();
	}, []);
	const {
		setPreviewDeviceType,
	} = useDispatch('ShopCredBlocks/data'); //replace ( core/edit-post) to ShopCredBlocks/data
	const customSetPreviewDeviceType = (device) => {
		setPreviewDeviceType(device);
	};
	const devices = [
		{
			name: 'Desktop',
			title: <Dashicon icon="desktop" />,
			itemClass: 'spc-desktop-tab spc-responsive-tabs',
		},
		{
			name: 'Tablet',
			title: <Dashicon icon="tablet" />,
			itemClass: 'spc-tablet-tab spc-responsive-tabs',
		},
		{
			name: 'Mobile',
			key: 'mobile',
			title: <Dashicon icon="smartphone" />,
			itemClass: 'spc-mobile-tab spc-responsive-tabs',
		},
	];
	const output = {};
	output.Desktop = (
		<Fragment></Fragment>
	);
	output.Tablet = (
		<Fragment></Fragment>
	);
	output.Mobile = (
		<Fragment></Fragment>
	);

	return (
		<div className={'spc-responsive-control-options'}>
			<div className="spc-size-type-field-tabs">
				<ButtonGroup className="components-tab-panel__tabs" aria-label={__('Device', 'shop-cred')}>
					{map(devices, ({ name, key, title, itemClass }) => (
						<Button
							key={key}
							className={`components-button components-tab-panel__tabs-item ${itemClass}${name === deviceType ? ' active-tab' : ''}`}
							aria-pressed={deviceType === name}
							onClick={() => customSetPreviewDeviceType(name)}
						>
							{title}
						</Button>
					))}
				</ButtonGroup>
				<div className="spc-responsive-control-inner">
					{(output[deviceType] ? output[deviceType] : output.Desktop)}
				</div>
			</div>
		</div>
	);
}