import { __ } from '@wordpress/i18n';
import classnames from "classnames"

export const CartButton = (props) => {

	const { attributes, post } = props

	let target = (attributes.newTab) ? "_blank" : "_self"

	let cta_text = (attributes.addToCartbtnText) ? attributes.addToCartbtnText : __("Add To Cart")

	if (attributes.displayProductCartbtn) {

		return (
			<div className={classnames('spc-button-wrapper wp-block-button')}>
				<a
					className={classnames(
						'add_to_cart_button button',
						'spc-cart-button',
					)}
					href={post.link}
					target={target}
					rel="noopener noreferrer">{cta_text}</a>
			</div>

		)

	} else {
		return null
	}

}
