const getSPCEditorStateLocalStorage = (key = false) => {
	if (!window.localStorage) {
		return null;
	}

	if (!key) {
		return localStorage;
	}

	const spcLastOpenedSettingState = localStorage.getItem(key);

	if (spcLastOpenedSettingState) {

		return JSON.parse(spcLastOpenedSettingState)
	}

	return null;
};

export default getSPCEditorStateLocalStorage;
