
import SPCAdvancedPanelBody from "../../components/advanced-panel-body"
import TypographyControl from "../../components/typography"
import BoxShadowControl from '../../components/box-shadow-control'
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'

import {
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
    Button,
    TextControl
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalProductPaginationControl = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // Filterable
        displayProductPagination,
        pageLimit,
        productPaginationMarkup,
        productPaginationPGLoadGoogleFonts,
        productPaginationPGFontSize,
        productPaginationPGFontSizeType,
        productPaginationPGFontSizeTablet,
        productPaginationPGFontSizeMobile,
        productPaginationPGFontFamily,
        productPaginationPGFontWeight,
        productPaginationPGLineHeightType,
        productPaginationPGLineHeight,
        productPaginationPGLineHeightTablet,
        productPaginationPGLineHeightMobile,
        productPaginationPGFontSubset,
        productPaginationTextTransform,
        productPaginationLetterSpacing,
        productPaginationPGColor,
        productPaginationPGBgColor,
        productPaginationPGHcolor,
        productPaginationPGBgHcolor,
        productPaginationPGBorderStyle,
        productPaginationPGBorderWidth,
        productPaginationPGBorderColor,
        productPaginationPGBorderHStyle,
        productPaginationPGBorderHWidth,
        productPaginationPGBorderHColor,
        productPaginationPGSpaceing,
        productPaginationPGMargin,
        productPaginationPGMarginUnit,
        productPaginationSpecing,
        productPaginationPGPadding,
        productPaginationPGPaddingUnit,
        productPaginationPGBR,
        productPaginationPGBRUnit,
        productPaginationPGTopSpace,
        productPaginationPGBottomSpace,
        productPaginationPGBoxShadowColor,
        productPaginationPGBoxShadowHOffset,
        productPaginationPGBoxShadowVOffset,
        productPaginationPGBoxShadowBlur,
        productPaginationPGBoxShadowSpread,
        productPaginationPGBoxShadowPosition,
        productPaginationAlignment,
        productPaginationPrevText,
        productPaginationNextText,

    } = attributes;

    const onChangePageLimit = (value) => {
        const { setAttributes } = props

        setAttributes({ pageLimit: value })
        setAttributes({ productPaginationMarkup: "empty" })
    }

    const onChangePrevText = (value) => {
        const { setAttributes } = props

        setAttributes({ productPaginationPrevText: value })
        setAttributes({ productPaginationMarkup: "empty" })
    }

    const onChangeNextText = (value) => {
        const { setAttributes } = props

        setAttributes({ productPaginationNextText: value })
        setAttributes({ productPaginationMarkup: "empty" })
    }

    //margin start

    const [productPaginationPGMarginControl, setproductPaginationPGMarginControl] = useState('individual');

    const productPaginationPGMarginMin = (productPaginationPGMarginUnit === 'em' || productPaginationPGMarginUnit === 'rem' ? -12 : -200);
    const productPaginationPGMarginMax = (productPaginationPGMarginUnit === 'em' || productPaginationPGMarginUnit === 'rem' ? 24 : 200);
    const productPaginationPGMarginStep = (productPaginationPGMarginUnit === 'em' || productPaginationPGMarginUnit === 'rem' ? 0.1 : 1);

    const saveproductPaginationPGMargin = (value) => {
        const newUpdate = productPaginationPGMargin.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productPaginationPGMargin: newUpdate,
        });
    };
    //margin end   

    //padding start

    const [productPaginationPGPaddingControl, setproductPaginationPGPaddingControl] = useState('individual');

    const productPaginationPGPaddingMin = (productPaginationPGPaddingUnit === 'em' || productPaginationPGPaddingUnit === 'rem' ? -12 : -200);
    const productPaginationPGPaddingMax = (productPaginationPGPaddingUnit === 'em' || productPaginationPGPaddingUnit === 'rem' ? 24 : 200);
    const productPaginationPGPaddingStep = (productPaginationPGPaddingUnit === 'em' || productPaginationPGPaddingUnit === 'rem' ? 0.1 : 1);

    const saveProductPaginationPGPadding = (value) => {
        const newUpdate = productPaginationPGPadding.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productPaginationPGPadding: newUpdate,
        });
    };
    //padding end    

    //border radius start

    const [productPaginationPGBRControl, setproductPaginationPGBRControl] = useState('individual');

    const productPaginationPGBRMin = (productPaginationPGBRUnit === 'em' || productPaginationPGBRUnit === 'rem' ? -12 : -200);
    const productPaginationPGBRMax = (productPaginationPGBRUnit === 'em' || productPaginationPGBRUnit === 'rem' ? 24 : 200);
    const productPaginationPGBRStep = (productPaginationPGBRUnit === 'em' || productPaginationPGBRUnit === 'rem' ? 0.1 : 1);

    const saveproductPaginationPGBR = (value) => {
        const newUpdate = productPaginationPGBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productPaginationPGBR: newUpdate,
        });
    };
    //border radius end   

    return (

        <>
            {displayProductPagination &&
                <SPCAdvancedPanelBody title={__("pagination", 'shop-cred')} initialOpen={false}>

                    {displayProductPagination == true &&
                        <div className="spc-full-range-wrapper">
                            <RangeControl
                                className='spc-range-control-wrapper'
                                label={__("Page Limit", 'shop-cred')}
                                value={pageLimit}
                                onChange={onChangePageLimit}
                                min={0}
                                max={100}
                            />
                        </div>
                    }

                    {displayProductPagination == true &&
                        <Fragment>
                            <hr className="spc-editor-separator" />
                            <TextControl
                                label={__("Previous Text", 'shop-cred')}
                                value={productPaginationPrevText}
                                onChange={onChangePrevText}
                            />
                            <TextControl
                                label={__("Next Text", 'shop-cred')}
                                value={productPaginationNextText}
                                onChange={onChangeNextText}
                            />

                            <TypographyControl
                                label={__("Typography", 'shop-cred')}
                                attributes={attributes}
                                setAttributes={setAttributes}
                                loadGoogleFonts={{ value: productPaginationPGLoadGoogleFonts, label: "productPaginationPGLoadGoogleFonts" }}
                                fontFamily={{ value: productPaginationPGFontFamily, label: "productPaginationPGFontFamily" }}
                                fontWeight={{ value: productPaginationPGFontWeight, label: "productPaginationPGFontWeight" }}
                                fontSubset={{ value: productPaginationPGFontSubset, label: "productPaginationPGFontSubset" }}
                                fontSizeType={{ value: productPaginationPGFontSizeType, label: "productPaginationPGFontSizeType" }}
                                fontSize={{ value: productPaginationPGFontSize, label: "productPaginationPGFontSize" }}
                                fontSizeMobile={{ value: productPaginationPGFontSizeMobile, label: "productPaginationPGFontSizeMobile" }}
                                fontSizeTablet={{ value: productPaginationPGFontSizeTablet, label: "productPaginationPGFontSizeTablet" }}
                                lineHeightType={{ value: productPaginationPGLineHeightType, label: "productPaginationPGLineHeightType" }}
                                lineHeight={{ value: productPaginationPGLineHeight, label: "productPaginationPGLineHeight" }}
                                lineHeightMobile={{ value: productPaginationPGLineHeightMobile, label: "productPaginationPGLineHeightMobile" }}
                                lineHeightTablet={{ value: productPaginationPGLineHeightTablet, label: "productPaginationPGLineHeightTablet" }}
                                textTransform={{ value: productPaginationTextTransform, label: 'productPaginationTextTransform' }}
                                letterSpacing={{ value: productPaginationLetterSpacing, label: 'productPaginationLetterSpacing' }}
                            />
                            <div className="spc-editor-alignment-group-wrapper">
                                <p> {__("Alignment", 'shop-cred')}</p>
                                <div className="spc-editor-alignment-group">
                                    <Button
                                        className={'spc-editor-alignleft'}
                                        key={"left"}
                                        icon="editor-alignleft"
                                        label="Left"
                                        onClick={() => setAttributes({ productPaginationAlignment: "left" })}
                                        aria-pressed={"left" === productPaginationAlignment}
                                        isPrimary={"left" === productPaginationAlignment}
                                    />
                                    <Button
                                        className={'spc-editor-aligncenter'}
                                        key={"center"}
                                        icon="editor-aligncenter"
                                        label="Center"
                                        onClick={() => setAttributes({ productPaginationAlignment: "center" })}
                                        aria-pressed={"center" === productPaginationAlignment}
                                        isPrimary={"center" === productPaginationAlignment}
                                    />
                                    <Button
                                        className={'spc-editor-alignright'}
                                        key={"right"}
                                        icon="editor-alignright"
                                        label="Right"
                                        onClick={() => setAttributes({ productPaginationAlignment: "right" })}
                                        aria-pressed={"right" === productPaginationAlignment}
                                        isPrimary={"right" === productPaginationAlignment}
                                    />
                                </div>
                            </div>
                            <hr className="spc-editor-separator" />

                            <PanelRow>
                                <div className="spc-full-range-wrapper">
                                    <RangeControl
                                        className='spc-range-control-wrapper'
                                        label={__("spacing (px)", 'shop-cred')}
                                        value={productPaginationSpecing}
                                        onChange={value => {
                                            setAttributes({ productPaginationSpecing: value })
                                        }}
                                        min={5}
                                        max={100}
                                    />
                                </div>
                            </PanelRow>

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Margin', 'shop-cred')}
                                    value={undefined !== productPaginationPGMargin && undefined !== productPaginationPGMargin[0] && undefined !== productPaginationPGMargin[0].desk ? productPaginationPGMargin[0].desk : ['20', '20', '20', '20']}
                                    control={productPaginationPGMarginControl}
                                    tabletValue={undefined !== productPaginationPGMargin && undefined !== productPaginationPGMargin[0] && undefined !== productPaginationPGMargin[0].tablet ? productPaginationPGMargin[0].tablet : ['10', '10', '10', '10']}
                                    mobileValue={undefined !== productPaginationPGMargin && undefined !== productPaginationPGMargin[0] && undefined !== productPaginationPGMargin[0].mobile ? productPaginationPGMargin[0].mobile : ['10', '10', '10', '10']}
                                    onChange={(value) => saveproductPaginationPGMargin({ desk: value })}
                                    onChangeTablet={(value) => saveproductPaginationPGMargin({ tablet: value })}
                                    onChangeMobile={(value) => saveproductPaginationPGMargin({ mobile: value })}
                                    onChangeControl={(value) => setproductPaginationPGMarginControl(value)}
                                    onChangeTabletControl={(value) => setproductPaginationPGMarginControl(value)}
                                    onChangeMobileControl={(value) => setproductPaginationPGMarginControl(value)}
                                    min={productPaginationPGMarginMin}
                                    max={productPaginationPGMarginMax}
                                    step={productPaginationPGMarginStep}
                                    unit={productPaginationPGMarginUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ productPaginationPGMarginUnit: value })}
                                />
                            </PanelRow>

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Padding', 'shop-cred')}
                                    value={undefined !== productPaginationPGPadding && undefined !== productPaginationPGPadding[0] && undefined !== productPaginationPGPadding[0].desk ? productPaginationPGPadding[0].desk : ['8', '15', '8', '15']}
                                    control={productPaginationPGPaddingControl}
                                    tabletValue={undefined !== productPaginationPGPadding && undefined !== productPaginationPGPadding[0] && undefined !== productPaginationPGPadding[0].tablet ? productPaginationPGPadding[0].tablet : ['8', '15', '8', '15']}
                                    mobileValue={undefined !== productPaginationPGPadding && undefined !== productPaginationPGPadding[0] && undefined !== productPaginationPGPadding[0].mobile ? productPaginationPGPadding[0].mobile : ['8', '15', '8', '15']}
                                    onChange={(value) => saveProductPaginationPGPadding({ desk: value })}
                                    onChangeTablet={(value) => saveProductPaginationPGPadding({ tablet: value })}
                                    onChangeMobile={(value) => saveProductPaginationPGPadding({ mobile: value })}
                                    onChangeControl={(value) => setproductPaginationPGPaddingControl(value)}
                                    onChangeTabletControl={(value) => setproductPaginationPGPaddingControl(value)}
                                    onChangeMobileControl={(value) => setproductPaginationPGPaddingControl(value)}
                                    min={productPaginationPGPaddingMin}
                                    max={productPaginationPGPaddingMax}
                                    step={productPaginationPGPaddingStep}
                                    unit={productPaginationPGPaddingUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ productPaginationPGPaddingUnit: value })}
                                />

                            </PanelRow>

                            <PanelRow className="spc-relative-row">
                                <ResponsiveMeasurementControls
                                    label={__('Border Radius', 'shop-cred')}
                                    value={undefined !== productPaginationPGBR && undefined !== productPaginationPGBR[0] && undefined !== productPaginationPGBR[0].desk ? productPaginationPGBR[0].desk : ['0', '0', '0', '0']}
                                    control={productPaginationPGBRControl}
                                    tabletValue={undefined !== productPaginationPGBR && undefined !== productPaginationPGBR[0] && undefined !== productPaginationPGBR[0].tablet ? productPaginationPGBR[0].tablet : ['0', '0', '0', '0']}
                                    mobileValue={undefined !== productPaginationPGBR && undefined !== productPaginationPGBR[0] && undefined !== productPaginationPGBR[0].mobile ? productPaginationPGBR[0].mobile : ['0', '0', '0', '0']}
                                    onChange={(value) => saveproductPaginationPGBR({ desk: value })}
                                    onChangeTablet={(value) => saveproductPaginationPGBR({ tablet: value })}
                                    onChangeMobile={(value) => saveproductPaginationPGBR({ mobile: value })}
                                    onChangeControl={(value) => setproductPaginationPGBRControl(value)}
                                    onChangeTabletControl={(value) => setproductPaginationPGBRControl(value)}
                                    onChangeMobileControl={(value) => setproductPaginationPGBRControl(value)}
                                    min={productPaginationPGBRMin}
                                    max={productPaginationPGBRMax}
                                    step={productPaginationPGBRStep}
                                    unit={productPaginationPGBRUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ productPaginationPGBRUnit: value })}
                                />

                            </PanelRow>

                            <hr className="spc-editor-separator" />

                            <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                                activeClass="active-tab"
                                tabs={[
                                    {
                                        name: "normal",
                                        title: __("Normal", 'shop-cred'),
                                        className: "spc-normal-tab spc-tabs-control",
                                    },
                                    {
                                        name: "hover",
                                        title: __("Active", 'shop-cred'),
                                        className: "spc-focus-tab spc-tabs-control",
                                    },
                                ]}>
                                {
                                    (tabName) => {
                                        let tabout_color
                                        if ("normal" === tabName.name) {
                                            tabout_color = <Fragment>

                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Text Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={productPaginationPGColor}
                                                        onChange={(hexColor) => setAttributes({ productPaginationPGColor: hexColor })}
                                                        allowReset
                                                    />
                                                </div>


                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Background Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={productPaginationPGBgColor}
                                                        onChange={(colorValue) => setAttributes({ productPaginationPGBgColor: colorValue })}
                                                        allowReset
                                                    />
                                                </div>
                                                <PanelRow>
                                                    <SelectControl
                                                        label={__("Border Style", 'shop-cred')}
                                                        className="spc-select-control-wrapper"
                                                        value={productPaginationPGBorderStyle}
                                                        options={[
                                                            { value: "none", label: __("None", 'shop-cred') },
                                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                            { value: "double", label: __("Double", 'shop-cred') },
                                                        ]}
                                                        onChange={value => {
                                                            setAttributes({ productPaginationPGBorderStyle: value })
                                                        }}
                                                    />
                                                </PanelRow>
                                                {"none" !== productPaginationPGBorderStyle &&
                                                    <PanelRow>
                                                        <div className="spc-full-range-wrapper">
                                                            <RangeControl
                                                                className='spc-range-control-wrapper'
                                                                label={__("Border Width (px)", 'shop-cred')}
                                                                value={productPaginationPGBorderWidth}
                                                                onChange={value => {
                                                                    setAttributes({ productPaginationPGBorderWidth: value })
                                                                }}
                                                                min={0}
                                                                max={20}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </PanelRow>
                                                }
                                                {"none" !== productPaginationPGBorderStyle &&
                                                    <>

                                                        <div className="spc-color-component-settings">
                                                            <p className="spc-color-component-custom-text">
                                                                {__("Border Color", 'shop-cred')}
                                                            </p>
                                                            <ColorPalette
                                                                className="spc-color-component-custom-class"
                                                                value={productPaginationPGBorderColor}
                                                                onChange={(value) => setAttributes({ productPaginationPGBorderColor: value })}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </>
                                                }

                                            </Fragment>
                                        } else {
                                            tabout_color = <Fragment>
                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Text Hover", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            lassName="spc-color-component-custom-class"
                                                            value={productPaginationPGHcolor}
                                                            onChange={(colorValue) => setAttributes({ productPaginationPGHcolor: colorValue })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>
                                                <>

                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Background Color", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={productPaginationPGBgHcolor}
                                                            onChange={(colorValue) => setAttributes({ productPaginationPGBgHcolor: colorValue })}
                                                            allowReset
                                                        />
                                                    </div>
                                                </>

                                                <PanelRow>
                                                    <SelectControl
                                                        label={__("Border Style", 'shop-cred')}
                                                        className="spc-select-control-wrapper"
                                                        value={productPaginationPGBorderHStyle}
                                                        options={[
                                                            { value: "none", label: __("None", 'shop-cred') },
                                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                            { value: "double", label: __("Double", 'shop-cred') },
                                                        ]}
                                                        onChange={value => {
                                                            setAttributes({ productPaginationPGBorderHStyle: value })
                                                        }}
                                                    />
                                                </PanelRow>
                                                {"none" !== productPaginationPGBorderHStyle &&
                                                    <PanelRow>
                                                        <div className="spc-full-range-wrapper">
                                                            <RangeControl
                                                                className='spc-range-control-wrapper'
                                                                label={__("Border Width (px)", 'shop-cred')}
                                                                value={productPaginationPGBorderHWidth}
                                                                onChange={value => {
                                                                    setAttributes({ productPaginationPGBorderHWidth: value })
                                                                }}
                                                                min={0}
                                                                max={20}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </PanelRow>
                                                }
                                                {"none" !== productPaginationPGBorderHStyle &&
                                                    <>

                                                        <div className="spc-color-component-settings">
                                                            <p className="spc-color-component-custom-text">
                                                                {__("Border Color", 'shop-cred')}
                                                            </p>
                                                            <ColorPalette
                                                                className="spc-color-component-custom-class"
                                                                value={productPaginationPGBorderHColor}
                                                                onChange={(value) => setAttributes({ productPaginationPGBorderHColor: value })}
                                                                allowReset
                                                            />
                                                        </div>
                                                    </>
                                                }
                                            </Fragment>
                                        }
                                        return <div>{tabout_color}</div>
                                    }
                                }
                            </TabPanel>

                            <hr className="spc-editor-separator" />

                            <PanelRow>
                                <BoxShadowControl {...props}
                                    attributes={attributes}
                                    setAttributes={setAttributes}
                                    label={__("Box Shadow", 'shop-cred')}
                                    boxShadowColor={'productPaginationPGBoxShadowColor'}
                                    boxShadowHOffset={'productPaginationPGBoxShadowHOffset'}
                                    boxShadowVOffset={'productPaginationPGBoxShadowVOffset'}
                                    boxShadowBlur={'productPaginationPGBoxShadowBlur'}
                                    boxShadowSpread={'productPaginationPGBoxShadowSpread'}
                                    boxShadowPosition={'productPaginationPGBoxShadowPosition'}
                                />
                            </PanelRow>

                        </Fragment>
                    }
                </SPCAdvancedPanelBody>
            }
        </>
    )
}

export default GlobalProductPaginationControl