import BoxShadowControl from '../../components/box-shadow-control'
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'

import SPCAdvancedPanelBody from "../../components/advanced-panel-body"

import {
    ToggleControl,
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
    Button
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    InspectorControls,
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalContainerController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // container
        equalHeight,
        gContainerAlignment,
        productGridMainMargin,
        productGridMainMarginUnit,
        gContainerBoxColor,
        gContainerBoxHColor,
        gContainerBoxBGColor,
        gContainerBoxBGHColor,
        gContainerBoxPadding,
        gContainerBoxPaddingUnit,
        gContainerBoxBR,
        gContainerBoxBRUnit,
        gContainerBoxBorderStyleNormal,
        gContainerBoxBorderWidthNormal,
        gContainerBoxBorderColorNormal,
        gContainerBoxBorderStyleHover,
        gContainerBoxBorderWidthHover,
        gContainerBoxBorderColorHover,
        gContainerBoxShadowColor,
        gContainerBoxShadowHOffset,
        gContainerBoxShadowVOffset,
        gContainerBoxShadowBlur,
        gContainerBoxShadowSpread,
        gContainerBoxShadowPosition,
        gContainerBoxShadowColorHover,
        gContainerBoxShadowHOffsetHover,
        gContainerBoxShadowVOffsetHover,
        gContainerBoxShadowBlurHover,
        gContainerBoxShadowSpreadHover,
        gContainerBoxShadowPositionHover,

    } = attributes;

    //margin start

    const [marginControl, setMarginControl] = useState('individual');

    const marginMin = (productGridMainMarginUnit === 'em' || productGridMainMarginUnit === 'rem' ? -12 : -200);
    const marginMax = (productGridMainMarginUnit === 'em' || productGridMainMarginUnit === 'rem' ? 24 : 200);
    const marginStep = (productGridMainMarginUnit === 'em' || productGridMainMarginUnit === 'rem' ? 0.1 : 1);

    const saveMargin = (value) => {
        const newUpdate = productGridMainMargin.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productGridMainMargin: newUpdate,
        });
    };

    //margin end   

    //padding start

    const [gContainerPaddingControl, setgContainerPaddingControl] = useState('individual');

    const paddingMin = (gContainerBoxPaddingUnit === 'em' || gContainerBoxPaddingUnit === 'rem' ? -12 : -200);
    const paddingMax = (gContainerBoxPaddingUnit === 'em' || gContainerBoxPaddingUnit === 'rem' ? 24 : 200);
    const paddingStep = (gContainerBoxPaddingUnit === 'em' || gContainerBoxPaddingUnit === 'rem' ? 0.1 : 1);

    const savePadding = (value) => {
        const newUpdate = gContainerBoxPadding.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gContainerBoxPadding: newUpdate,
        });
    };
    //padding end    

    //border radius start

    const [gContainerBRControl, setgContainerBR] = useState('individual');

    const BRMin = (gContainerBoxBRUnit === 'em' || gContainerBoxBRUnit === 'rem' ? -12 : -200);
    const BRMax = (gContainerBoxBRUnit === 'em' || gContainerBoxBRUnit === 'rem' ? 24 : 200);
    const BRStep = (gContainerBoxBRUnit === 'em' || gContainerBoxBRUnit === 'rem' ? 0.1 : 1);

    const saveBR = (value) => {
        const newUpdate = gContainerBoxBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gContainerBoxBR: newUpdate,
        });
    };
    //border radius end


    return (

        <SPCAdvancedPanelBody title={__("Container", 'shop-cred')} initialOpen={false}>

            <ToggleControl
                label={__("Equal Height", 'shop-cred')}
                checked={equalHeight}
                onChange={(value) => setAttributes({ equalHeight: !equalHeight })}
            />

            <div className="spc-editor-alignment-group-wrapper">
                <p> {__("Alignment", 'shop-cred')}</p>
                <div className="spc-editor-alignment-group">
                    <Button
                        className={'spc-editor-alignleft'}
                        key={"left"}
                        icon="editor-alignleft"
                        label="Left"
                        onClick={() => setAttributes({ gContainerAlignment: "left" })}
                        aria-pressed={"left" === gContainerAlignment}
                        isPrimary={"left" === gContainerAlignment}
                    />
                    <Button
                        className={'spc-editor-aligncenter'}
                        key={"center"}
                        icon="editor-aligncenter"
                        label="Center"
                        onClick={() => setAttributes({ gContainerAlignment: "center" })}
                        aria-pressed={"center" === gContainerAlignment}
                        isPrimary={"center" === gContainerAlignment}
                    />
                    <Button
                        className={'spc-editor-alignright'}
                        key={"end"}
                        icon="editor-alignright"
                        label="Right"
                        onClick={() => setAttributes({ gContainerAlignment: "end" })}
                        aria-pressed={"end" === gContainerAlignment}
                        isPrimary={"end" === gContainerAlignment}
                    />
                </div>
            </div>
            <PanelRow className="spc-relative-row">
                <ResponsiveMeasurementControls
                    label={__('Container Margin', 'shop-cred')}
                    value={undefined !== productGridMainMargin && undefined !== productGridMainMargin[0] && undefined !== productGridMainMargin[0].desk ? productGridMainMargin[0].desk : ['20', '20', '20', '20']}
                    control={marginControl}
                    tabletValue={undefined !== productGridMainMargin && undefined !== productGridMainMargin[0] && undefined !== productGridMainMargin[0].tablet ? productGridMainMargin[0].tablet : ['10', '10', '10', '10']}
                    mobileValue={undefined !== productGridMainMargin && undefined !== productGridMainMargin[0] && undefined !== productGridMainMargin[0].mobile ? productGridMainMargin[0].mobile : ['10', '10', '10', '10']}
                    onChange={(value) => saveMargin({ desk: value })}
                    onChangeTablet={(value) => saveMargin({ tablet: value })}
                    onChangeMobile={(value) => saveMargin({ mobile: value })}
                    onChangeControl={(value) => setMarginControl(value)}
                    onChangeTabletControl={(value) => setMarginControl(value)}
                    onChangeMobileControl={(value) => setMarginControl(value)}
                    min={marginMin}
                    max={marginMax}
                    step={marginStep}
                    unit={productGridMainMarginUnit}
                    units={['px', 'em', 'rem']}
                    onUnit={(value) => setAttributes({ productGridMainMarginUnit: value })}
                />
            </PanelRow>
            <PanelRow className="spc-relative-row">
                <ResponsiveMeasurementControls
                    label={__('Padding', 'shop-cred')}
                    value={undefined !== gContainerBoxPadding && undefined !== gContainerBoxPadding[0] && undefined !== gContainerBoxPadding[0].desk ? gContainerBoxPadding[0].desk : ['20', '20', '20', '20']}
                    control={gContainerPaddingControl}
                    tabletValue={undefined !== gContainerBoxPadding && undefined !== gContainerBoxPadding[0] && undefined !== gContainerBoxPadding[0].tablet ? gContainerBoxPadding[0].tablet : ['10', '10', '10', '10']}
                    mobileValue={undefined !== gContainerBoxPadding && undefined !== gContainerBoxPadding[0] && undefined !== gContainerBoxPadding[0].mobile ? gContainerBoxPadding[0].mobile : ['10', '10', '10', '10']}
                    onChange={(value) => savePadding({ desk: value })}
                    onChangeTablet={(value) => savePadding({ tablet: value })}
                    onChangeMobile={(value) => savePadding({ mobile: value })}
                    onChangeControl={(value) => setgContainerPaddingControl(value)}
                    onChangeTabletControl={(value) => setgContainerPaddingControl(value)}
                    onChangeMobileControl={(value) => setgContainerPaddingControl(value)}
                    min={paddingMin}
                    max={paddingMax}
                    step={paddingStep}
                    unit={gContainerBoxPaddingUnit}
                    units={['px', 'em', '%']}
                    onUnit={(value) => setAttributes({ gContainerBoxPaddingUnit: value })}
                />
            </PanelRow>

            <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                activeClass="active-tab"
                tabs={[
                    {
                        name: "normal",
                        title: __("Normal", 'shop-cred'),
                        className: "spc-normal-tab spc-tabs-control",
                    },
                    {
                        name: "hover",
                        title: __("Hover", 'shop-cred'),
                        className: "spc-focus-tab spc-tabs-control",
                    },
                ]}>
                {
                    (tabName) => {
                        let tabout_color
                        if ("normal" === tabName.name) {
                            tabout_color = <Fragment>

                                <div className="spc-color-component-settings">
                                    <p className="spc-color-component-custom-text">
                                        {__("Background Color", 'shop-cred')}
                                    </p>

                                    <ColorPalette
                                        className="spc-color-component-custom-class"
                                        value={gContainerBoxBGColor}
                                        onChange={(colorValue) => setAttributes({ gContainerBoxBGColor: colorValue })}
                                        enableAlpha
                                    />

                                </div>

                                <PanelRow>
                                    <SelectControl
                                        label={__("Border Style", 'shop-cred')}
                                        className="spc-select-control-wrapper"
                                        value={gContainerBoxBorderStyleNormal}
                                        options={[
                                            { value: "none", label: __("None", 'shop-cred') },
                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                            { value: "double", label: __("Double", 'shop-cred') },
                                        ]}
                                        onChange={value => {
                                            setAttributes({ gContainerBoxBorderStyleNormal: value })
                                        }}
                                    />
                                </PanelRow>
                                {"none" !== gContainerBoxBorderStyleNormal &&
                                    <PanelRow>
                                        <div className="spc-full-range-wrapper">
                                            <RangeControl
                                                className='spc-range-control-wrapper'
                                                label={__("Border Width (px)", 'shop-cred')}
                                                value={gContainerBoxBorderWidthNormal}
                                                onChange={value => {
                                                    setAttributes({ gContainerBoxBorderWidthNormal: value })
                                                }}
                                                min={0}
                                                max={20}
                                                allowReset
                                            />
                                        </div>
                                    </PanelRow>
                                }
                                {"none" !== gContainerBoxBorderStyleNormal &&
                                    <>

                                        <div className="spc-color-component-settings">
                                            <p className="spc-color-component-custom-text">
                                                {__("Border Color", 'shop-cred')}
                                            </p>
                                            <ColorPalette
                                                className="spc-color-component-custom-class"
                                                value={gContainerBoxBorderColorNormal}
                                                onChange={(value) => setAttributes({ gContainerBoxBorderColorNormal: value })}
                                                allowReset
                                                enableAlpha
                                            />
                                        </div>
                                    </>
                                }

                                <ResponsiveMeasurementControls
                                    label={__('Border Radius', 'shop-cred')}
                                    value={undefined !== gContainerBoxBR && undefined !== gContainerBoxBR[0] && undefined !== gContainerBoxBR[0].desk ? gContainerBoxBR[0].desk : ['0', '0', '0', '0']}
                                    control={gContainerBRControl}
                                    tabletValue={undefined !== gContainerBoxBR && undefined !== gContainerBoxBR[0] && undefined !== gContainerBoxBR[0].tablet ? gContainerBoxBR[0].tablet : ['0', '0', '0', '0']}
                                    mobileValue={undefined !== gContainerBoxBR && undefined !== gContainerBoxBR[0] && undefined !== gContainerBoxBR[0].mobile ? gContainerBoxBR[0].mobile : ['0', '0', '0', '0']}
                                    onChange={(value) => saveBR({ desk: value })}
                                    onChangeTablet={(value) => saveBR({ tablet: value })}
                                    onChangeMobile={(value) => saveBR({ mobile: value })}
                                    onChangeControl={(value) => setgContainerBR(value)}
                                    onChangeTabletControl={(value) => setgContainerBR(value)}
                                    onChangeMobileControl={(value) => setgContainerBR(value)}
                                    min={BRMin}
                                    max={BRMax}
                                    step={BRStep}
                                    unit={gContainerBoxBRUnit}
                                    units={['px', 'em', '%']}
                                    onUnit={(value) => setAttributes({ gContainerBoxBRUnit: value })}
                                />

                                <PanelRow>
                                    <BoxShadowControl {...props}
                                        attributes={attributes}
                                        setAttributes={setAttributes}
                                        label={__("Box Shadow", 'shop-cred')}
                                        boxShadowColor={'gContainerBoxShadowColor'}
                                        boxShadowHOffset={'gContainerBoxShadowHOffset'}
                                        boxShadowVOffset={'gContainerBoxShadowVOffset'}
                                        boxShadowBlur={'gContainerBoxShadowBlur'}
                                        boxShadowSpread={'gContainerBoxShadowSpread'}
                                        boxShadowPosition={'gContainerBoxShadowPosition'}
                                    />
                                </PanelRow>
                            </Fragment>
                        } else {
                            tabout_color = <Fragment>

                                <div className="spc-color-component-settings">
                                    <p className="spc-color-component-custom-text">
                                        {__("Background Color", 'shop-cred')}
                                    </p>
                                    <ColorPalette
                                        className="spc-color-component-custom-class"
                                        value={gContainerBoxBGHColor}
                                        onChange={(colorValue) => setAttributes({ gContainerBoxBGHColor: colorValue })}
                                        enableAlpha
                                    />
                                </div>

                                <PanelRow>
                                    <SelectControl
                                        label={__("Border Style", 'shop-cred')}
                                        className="spc-select-control-wrapper"
                                        value={gContainerBoxBorderStyleHover}
                                        options={[
                                            { value: "none", label: __("None", 'shop-cred') },
                                            { value: "solid", label: __("Solid", 'shop-cred') },
                                            { value: "dotted", label: __("Dotted", 'shop-cred') },
                                            { value: "dashed", label: __("Dashed", 'shop-cred') },
                                            { value: "double", label: __("Double", 'shop-cred') },
                                        ]}
                                        onChange={value => {
                                            setAttributes({ gContainerBoxBorderStyleHover: value })
                                        }}
                                    />
                                </PanelRow>
                                {"none" !== gContainerBoxBorderStyleHover &&
                                    <PanelRow>
                                        <div className="spc-full-range-wrapper">
                                            <RangeControl
                                                className='spc-range-control-wrapper'
                                                label={__("Border Width (px)", 'shop-cred')}
                                                value={gContainerBoxBorderWidthHover}
                                                onChange={value => {
                                                    setAttributes({ gContainerBoxBorderWidthHover: value })
                                                }}
                                                min={0}
                                                max={20}
                                                allowReset
                                            />
                                        </div>
                                    </PanelRow>
                                }
                                {"none" !== gContainerBoxBorderStyleHover &&
                                    <>
                                        <hr className="spc-editor-separator" />
                                        <div className="spc-color-component-settings">
                                            <p className="spc-color-component-custom-text">
                                                {__("Border Color", 'shop-cred')}
                                            </p>
                                            <ColorPalette
                                                className="spc-color-component-custom-class"
                                                value={gContainerBoxBorderColorHover}
                                                onChange={(value) => setAttributes({ gContainerBoxBorderColorHover: value })}
                                                allowReset
                                                enableAlpha
                                            />
                                        </div>
                                    </>
                                }

                                <PanelRow>
                                    <BoxShadowControl {...props}
                                        attributes={attributes}
                                        setAttributes={setAttributes}
                                        label={__("Box Shadow", 'shop-cred')}
                                        boxShadowColor={'gContainerBoxShadowColorHover'}
                                        boxShadowHOffset={'gContainerBoxShadowHOffsetHover'}
                                        boxShadowVOffset={'gContainerBoxShadowVOffsetHover'}
                                        boxShadowBlur={'gContainerBoxShadowBlurHover'}
                                        boxShadowSpread={'gContainerBoxShadowSpreadHover'}
                                        boxShadowPosition={'gContainerBoxShadowPositionHover'}
                                    />
                                </PanelRow>
                            </Fragment>
                        }
                        return <div>{tabout_color}</div>
                    }
                }
            </TabPanel>

        </SPCAdvancedPanelBody>
    )
}

export default GlobalContainerController