import SPCAdvancedPanelBody from "../../components/advanced-panel-body"
import BoxShadowControl from '../../components/box-shadow-control'
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'

import {
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
    Button
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalSwiperNavController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // Swiper Arrows
        productCarouselNavigation,
        gSwiperArrowsIconColor,
        gSwiperArrowsIconHColor,
        gSwiperArrowsBGColor,
        gSwiperArrowsBGHColor,
        gSwiperArrowsBoxSize,
        gSwiperArrowsBoxSizeTablet,
        gSwiperArrowsBoxSizeMobile,
        gSwiperArrowsIconSize,
        gSwiperArrowsIconSizeTablet,
        gSwiperArrowsIconSizeMobile,
        gSwiperLeftArrowsPosX,
        gSwiperLeftArrowsPosXTablet,
        gSwiperLeftArrowsPosXMobile,
        gSwiperLeftArrowsPosXUnit,
        gSwiperLeftArrowsPosXSyncUnits,
        gSwiperLeftArrowsPosY,
        gSwiperLeftArrowsPosYTablet,
        gSwiperLeftArrowsPosYMobile,
        gSwiperLeftArrowsPosYUnit,
        gSwiperLeftArrowsPosYSyncUnits,
        gSwiperRightArrowsPosX,
        gSwiperRightArrowsPosXTablet,
        gSwiperRightArrowsPosXMobile,
        gSwiperRightArrowsPosXUnit,
        gSwiperRightArrowsPosXSyncUnits,
        gSwiperRightArrowsPosY,
        gSwiperRightArrowsPosYTablet,
        gSwiperRightArrowsPosYMobile,
        gSwiperRightArrowsPosYUnit,
        gSwiperRightArrowsPosYSyncUnits,
        gSwiperArrowsBR,
        gSwiperArrowsBRUnit,
        gSwiperArrowsBRH,
        gSwiperArrowsBRHUnit,
        gSwiperArrowsBorderStyleNormal,
        gSwiperArrowsBorderWidthNormal,
        gSwiperArrowsBorderColorNormal,
        gSwiperArrowsBorderStyleHover,
        gSwiperArrowsBorderWidthHover,
        gSwiperArrowsBorderColorHover,
        gSwiperArrowsShadowColor,
        gSwiperArrowsShadowHOffset,
        gSwiperArrowsShadowVOffset,
        gSwiperArrowsShadowBlur,
        gSwiperArrowsShadowSpread,
        gSwiperArrowsShadowPosition,
        gSwiperArrowsShadowColorHover,
        gSwiperArrowsShadowHOffsetHover,
        gSwiperArrowsShadowVOffsetHover,
        gSwiperArrowsShadowBlurHover,
        gSwiperArrowsShadowSpreadHover,
        gSwiperArrowsShadowPositionHover

    } = attributes;

    //border radius start

    const [gSwiperArrowsBRControl, setgSwiperArrowsBRControl] = useState('individual');

    const gSwiperArrowsBRMin = (gSwiperArrowsBRUnit === 'em' || gSwiperArrowsBRUnit === 'rem' ? -12 : -200);
    const gSwiperArrowsBRMax = (gSwiperArrowsBRUnit === 'em' || gSwiperArrowsBRUnit === 'rem' ? 24 : 200);
    const gSwiperArrowsBRStep = (gSwiperArrowsBRUnit === 'em' || gSwiperArrowsBRUnit === 'rem' ? 0.1 : 1);

    const savegSwiperArrowsBR = (value) => {
        const newUpdate = gSwiperArrowsBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gSwiperArrowsBR: newUpdate,
        });
    };

    //border radius end

    //border radius ACTIVE start

    const [gSwiperArrowsBRHControl, setgSwiperArrowsBRHControl] = useState('individual');

    const gSwiperArrowsBRHMin = (gSwiperArrowsBRHUnit === 'em' || gSwiperArrowsBRHUnit === 'rem' ? -12 : -200);
    const gSwiperArrowsBRHMax = (gSwiperArrowsBRHUnit === 'em' || gSwiperArrowsBRHUnit === 'rem' ? 24 : 200);
    const gSwiperArrowsBRHStep = (gSwiperArrowsBRHUnit === 'em' || gSwiperArrowsBRHUnit === 'rem' ? 0.1 : 1);

    const savegSwiperArrowsBRH = (value) => {
        const newUpdate = gSwiperArrowsBRH.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gSwiperArrowsBRH: newUpdate,
        });
    };
    //border radius ACTIVE end

    return (

        <>

            <SPCAdvancedPanelBody title={__("Arrows", 'shop-cred')} initialOpen={false}>

                <hr className="spc-editor-separator" />

                <PanelRow className="spc-relative-row">
                    <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                        activeClass="active-tab"
                        tabs={[
                            {
                                name: "normal",
                                title: __("Normal", 'shop-cred'),
                                className: "spc-normal-tab spc-tabs-control",
                            },
                            {
                                name: "hover",
                                title: __("Hover", 'shop-cred'),
                                className: "spc-focus-tab spc-tabs-control",
                            },
                        ]}>
                        {
                            (tabName) => {
                                let tabout_color
                                if ("normal" === tabName.name) {
                                    tabout_color = <Fragment>
                                        <>
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Normal Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperArrowsIconColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperArrowsIconColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>

                                        <>

                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Background Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperArrowsBGColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperArrowsBGColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <PanelRow>
                                            <SelectControl
                                                label={__("Border Style", 'shop-cred')}
                                                className="spc-select-control-wrapper"
                                                value={gSwiperArrowsBorderStyleNormal}
                                                options={[
                                                    { value: "none", label: __("None", 'shop-cred') },
                                                    { value: "solid", label: __("Solid", 'shop-cred') },
                                                    { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                    { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                    { value: "double", label: __("Double", 'shop-cred') },
                                                ]}
                                                onChange={value => {
                                                    setAttributes({ gSwiperArrowsBorderStyleNormal: value })
                                                }}
                                            />
                                        </PanelRow>
                                        {"none" !== gSwiperArrowsBorderStyleNormal &&
                                            <PanelRow>
                                                <div className="spc-full-range-wrapper">
                                                    <RangeControl
                                                        className='spc-range-control-wrapper'
                                                        label={__("Border Width (px)", 'shop-cred')}
                                                        value={gSwiperArrowsBorderWidthNormal}
                                                        onChange={value => {
                                                            setAttributes({ gSwiperArrowsBorderWidthNormal: value })
                                                        }}
                                                        min={0}
                                                        max={20}
                                                        allowReset
                                                    />
                                                </div>
                                            </PanelRow>
                                        }
                                        {"none" !== gSwiperArrowsBorderStyleNormal &&
                                            <>
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Border Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gSwiperArrowsBorderColorNormal}
                                                        onChange={(value) => setAttributes({ gSwiperArrowsBorderColorNormal: value })}
                                                        allowReset
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                        }
                                        <PanelRow className="spc-relative-row">
                                            <ResponsiveMeasurementControls
                                                label={__('Border Radius', 'shop-cred')}
                                                value={undefined !== gSwiperArrowsBR && undefined !== gSwiperArrowsBR[0] && undefined !== gSwiperArrowsBR[0].desk ? gSwiperArrowsBR[0].desk : ['20', '20', '20', '20']}
                                                control={gSwiperArrowsBRControl}
                                                tabletValue={undefined !== gSwiperArrowsBR && undefined !== gSwiperArrowsBR[0] && undefined !== gSwiperArrowsBR[0].tablet ? gSwiperArrowsBR[0].tablet : ['10', '10', '10', '10']}
                                                mobileValue={undefined !== gSwiperArrowsBR && undefined !== gSwiperArrowsBR[0] && undefined !== gSwiperArrowsBR[0].mobile ? gSwiperArrowsBR[0].mobile : ['10', '10', '10', '10']}
                                                onChange={(value) => savegSwiperArrowsBR({ desk: value })}
                                                onChangeTablet={(value) => savegSwiperArrowsBR({ tablet: value })}
                                                onChangeMobile={(value) => savegSwiperArrowsBR({ mobile: value })}
                                                onChangeControl={(value) => setgSwiperArrowsBRControl(value)}
                                                onChangeTabletControl={(value) => setgSwiperArrowsBRControl(value)}
                                                onChangeMobileControl={(value) => setgSwiperArrowsBRControl(value)}
                                                min={gSwiperArrowsBRMin}
                                                max={gSwiperArrowsBRMax}
                                                step={gSwiperArrowsBRStep}
                                                unit={gSwiperArrowsBRUnit}
                                                units={['px', 'em', '%']}
                                                onUnit={(value) => setAttributes({ gSwiperArrowsBRUnit: value })}
                                            />
                                        </PanelRow>

                                        <PanelRow>
                                            <BoxShadowControl {...props}
                                                attributes={attributes}
                                                setAttributes={setAttributes}
                                                label={__("Box Shadow", 'shop-cred')}
                                                boxShadowColor={'gSwiperArrowsShadowColor'}
                                                boxShadowHOffset={'gSwiperArrowsShadowHOffset'}
                                                boxShadowVOffset={'gSwiperArrowsShadowVOffset'}
                                                boxShadowBlur={'gSwiperArrowsShadowBlur'}
                                                boxShadowSpread={'gSwiperArrowsShadowSpread'}
                                                boxShadowPosition={'gSwiperArrowsShadowPosition'}
                                            />
                                        </PanelRow>
                                    </Fragment>
                                } else {
                                    tabout_color = <Fragment>

                                        <>
                                            <hr className="spc-editor-separator" />
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Hover Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperArrowsIconHColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperArrowsIconHColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <>
                                            <hr className="spc-editor-separator" />
                                            <div className="spc-color-component-settings">
                                                <p className="spc-color-component-custom-text">
                                                    {__("Background Color", 'shop-cred')}
                                                </p>
                                                <ColorPalette
                                                    className="spc-color-component-custom-class"
                                                    value={gSwiperArrowsBGHColor}
                                                    onChange={(colorValue) => setAttributes({ gSwiperArrowsBGHColor: colorValue })}
                                                    enableAlpha
                                                />
                                            </div>
                                        </>
                                        <PanelRow>
                                            <SelectControl
                                                label={__("Border Style", 'shop-cred')}
                                                className="spc-select-control-wrapper"
                                                value={gSwiperArrowsBorderStyleHover}
                                                options={[
                                                    { value: "none", label: __("None", 'shop-cred') },
                                                    { value: "solid", label: __("Solid", 'shop-cred') },
                                                    { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                    { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                    { value: "double", label: __("Double", 'shop-cred') },
                                                ]}
                                                onChange={value => {
                                                    setAttributes({ gSwiperArrowsBorderStyleHover: value })
                                                }}
                                            />
                                        </PanelRow>
                                        {"none" !== gSwiperArrowsBorderStyleHover &&
                                            <PanelRow>
                                                <div className="spc-full-range-wrapper">
                                                    <RangeControl
                                                        className='spc-range-control-wrapper'
                                                        label={__("Border Width (px)", 'shop-cred')}
                                                        value={gSwiperArrowsBorderWidthHover}
                                                        onChange={value => {
                                                            setAttributes({ gSwiperArrowsBorderWidthHover: value })
                                                        }}
                                                        min={0}
                                                        max={20}
                                                        allowReset
                                                    />
                                                </div>
                                            </PanelRow>
                                        }
                                        {"none" !== gSwiperArrowsBorderStyleHover &&
                                            <>
                                                <hr className="spc-editor-separator" />
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Border Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gSwiperArrowsBorderColorHover}
                                                        onChange={(value) => setAttributes({ gSwiperArrowsBorderColorHover: value })}
                                                        allowReset
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                        }
                                        <PanelRow className="spc-relative-row">
                                            <ResponsiveMeasurementControls
                                                label={__('Border Radius', 'shop-cred')}
                                                value={undefined !== gSwiperArrowsBRH && undefined !== gSwiperArrowsBRH[0] && undefined !== gSwiperArrowsBRH[0].desk ? gSwiperArrowsBRH[0].desk : ['50', '50', '50', '50']}
                                                control={gSwiperArrowsBRHControl}
                                                tabletValue={undefined !== gSwiperArrowsBRH && undefined !== gSwiperArrowsBRH[0] && undefined !== gSwiperArrowsBRH[0].tablet ? gSwiperArrowsBRH[0].tablet : ['50', '50', '50', '50']}
                                                mobileValue={undefined !== gSwiperArrowsBRH && undefined !== gSwiperArrowsBRH[0] && undefined !== gSwiperArrowsBRH[0].mobile ? gSwiperArrowsBRH[0].mobile : ['50', '50', '50', '50']}
                                                onChange={(value) => savegSwiperArrowsBRH({ desk: value })}
                                                onChangeTablet={(value) => savegSwiperArrowsBRH({ tablet: value })}
                                                onChangeMobile={(value) => savegSwiperArrowsBRH({ mobile: value })}
                                                onChangeControl={(value) => setgSwiperArrowsBRHControl(value)}
                                                onChangeTabletControl={(value) => setgSwiperArrowsBRHControl(value)}
                                                onChangeMobileControl={(value) => setgSwiperArrowsBRHControl(value)}
                                                min={gSwiperArrowsBRHMin}
                                                max={gSwiperArrowsBRHMax}
                                                step={gSwiperArrowsBRStep}
                                                unit={gSwiperArrowsBRHUnit}
                                                units={['px', 'em', '%']}
                                                onUnit={(value) => setAttributes({ gSwiperArrowsBRHUnit: value })}
                                            />
                                        </PanelRow>
                                        <PanelRow>
                                            <BoxShadowControl {...props}
                                                attributes={attributes}
                                                setAttributes={setAttributes}
                                                label={__("Box Shadow", 'shop-cred')}
                                                boxShadowColor={'gSwiperArrowsShadowColorHover'}
                                                boxShadowHOffset={'gSwiperArrowsShadowHOffsetHover'}
                                                boxShadowVOffset={'gSwiperArrowsShadowVOffsetHover'}
                                                boxShadowBlur={'gSwiperArrowsShadowBlurHover'}
                                                boxShadowSpread={'gSwiperArrowsShadowSpreadHover'}
                                                boxShadowPosition={'gSwiperArrowsShadowPositionHover'}
                                            />
                                        </PanelRow>
                                    </Fragment>
                                }
                                return <div>{tabout_color}</div>
                            }
                        }
                    </TabPanel>
                </PanelRow>
            </SPCAdvancedPanelBody>

        </>
    )
}

export default GlobalSwiperNavController