/**
 * Range Control
 *
 */

/**
 * Internal block libraries
 */
import { __ } from '@wordpress/i18n';
import "../editor.scss"
import { RangeControl as CoreRangeControl } from '@wordpress/components';
import { useInstanceId } from '@wordpress/compose';
import {
	Button,
	DropdownMenu,
} from '@wordpress/components';

import {
	pxIcon,
	emIcon,
	remIcon,
	vhIcon,
	vwIcon,
	percentIcon,
} from '../../unit-icon';

let icons = {
	px: pxIcon,
	em: emIcon,
	rem: remIcon,
	vh: vhIcon,
	vw: vwIcon,
	percent: percentIcon,
};
/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function RangeControl({
	label,
	onChange,
	value = '',
	className = '',
	step = 1,
	max = 100,
	min = 0,
	beforeIcon = '',
	help = '',
	unit = '',
	onUnit,
	showUnit = false,
	units = ['px', 'em', '%'],
}) {
	/**
	 * Build Toolbar Items.
	 *
	 * @param {string} mappedUnit the unit.
	 * @returns {array} the unit array.
	 */
	const createLevelControlToolbar = (mappedUnit) => {
		return [{
			icon: (mappedUnit === '%' ? icons.percent : icons[mappedUnit]),
			isActive: unit === mappedUnit,
			onClick: () => {
				onUnit(mappedUnit);
			},
		}];
	};
	const POPOVER_PROPS = {
		className: 'spc-units-popover',
	};
	return [
		onChange && (
			<div className={`components-base-control spc-range-control${className ? ' ' + className : ''}`}>
				{label && (
					<label className="components-base-control__label">{label}</label>
				)}
				<div className={'spc-controls-content'}>
					<div className={'spc-full-range-wrapper spc-range-control-inner'}>
						<CoreRangeControl
							className='spc-range-control-wrapper'
							beforeIcon={beforeIcon}
							value={value}
							onChange={(newVal) => onChange(newVal)}
							min={min}
							max={max}
							step={step}
							help={help}
							allowReset={true}
						/>
					</div>
					{(onUnit || showUnit) && (
						<div className="spc-units">
							{units.length === 1 ? (
								<Button
									className="is-active is-single"
									isSmall
									disabled
								>{('%' === unit ? icons.percent : icons[unit])}</Button>
							) : (
								<DropdownMenu
									icon={('%' === unit ? icons.percent : icons[unit])}
									label={__('Select a Unit', 'spc-blocks')}
									controls={units.map((singleUnit) => createLevelControlToolbar(singleUnit))}
									className={'spc-units-group'}
									popoverProps={POPOVER_PROPS}
								/>
							)}
						</div>
					)}
				</div>
			</div>
		),
	];
}
