const { decodeEntities } = wp.htmlEntities
import { __ } from '@wordpress/i18n';

export const ProductPrice = (props) => {

	const { attributes, post } = props

	if (undefined == post.spc_product_price) {
		return null
	}

	if (attributes.displayProductPricing) {
		return (
			<div className='spc-product-price'>
				<div dangerouslySetInnerHTML={{ __html: post.spc_product_price }} />
			</div>
		)
	} else {
		return null
	}

}
