/**
 * Measure Component
 *
 */

/**
 * Import Css
 */
import './editor.scss';

/**
 * Import External
 */
import { isEqual } from 'lodash';
import MeasurementSingleControl from '../single-input-control';
// import RangeControl from '../../range/range-control';
import { image } from '@wordpress/icons';

// export { default as getUnitIcon } from '../unit-icon';
import { getUnitIcon } from '../unit-icon/helpers';
/**
 * Import Icons
 */
import {
    percentIcon,
    linkedIcon,
    individualIcon
} from '../../unit-icon';

import icons from '../../../utils/icons';


/**
 * Internal block libraries
 */
import { __ } from '@wordpress/i18n';
import { useRef, Fragment } from '@wordpress/element';
import {
    Button,
    DropdownMenu,
    Tooltip,
    RangeControl
} from '@wordpress/components';

/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function MeasurementControls({
    label,
    measurement,
    control = 'individual',
    onChange,
    onControl,
    step = 1,
    max = 100,
    min = 0,
    firstIcon = '',
    secondIcon = '',
    thirdIcon = '',
    fourthIcon = '',
    linkIcon = linkedIcon,
    unlinkIcon = individualIcon,
    isBorderRadius = false,
    unit = '',
    onUnit,
    showUnit = false,
    units = ['px', 'em', 'rem'],
    allowEmpty = false,
    key,
    className = '',
    reset,
    preset = '',
}) {
    const measureIcons = {
        first: isBorderRadius ? '' : firstIcon,
        second: isBorderRadius ? '' : secondIcon,
        third: isBorderRadius ? '' : thirdIcon,
        fourth: isBorderRadius ? '' : fourthIcon,
        link: linkIcon,
        unlink: unlinkIcon,
    }
    const zero = (allowEmpty ? '' : 0);
    /**
     * Build Toolbar Items.
     *
     * @param {string} mappedUnit the unit.
     * @returns {array} the unit array.
     */
    const createLevelControlToolbar = (mappedUnit) => {
        return [{
            icon: (mappedUnit === '%' ? percentIcon : getUnitIcon(mappedUnit)),
            // icon: (mappedUnit === '%' ? image : image),
            isActive: unit === mappedUnit,
            onClick: () => {
                onUnit(mappedUnit);
            },
        }];
    };
    const POPOVER_PROPS = {
        className: 'spc-units-popover',
    };
    const onReset = () => {
        if (typeof reset === 'function') {
            reset();
        } else {
            onChange(['', '', '', '']);
        }
    }
    return (
        <>
            {onChange && (
                <div key={key} className={`components-base-control spc-measure-control ${measureIcons.first !== '' ? 'spc-measure-corners-control' : 'spc-measure-sides-control'}${'' !== className ? ' ' + className : ''}`}>
                    {label && (
                        <div className="spc-title-bar">
                            {reset && (
                                <Button
                                    className="is-reset is-single"
                                    isSmall
                                    disabled={((isEqual(['', '', '', ''], measurement) || isEqual(['', 'auto', '', 'auto'], measurement)) ? true : false)}
                                    icon={undo}
                                    onClick={() => onReset()}
                                ></Button>
                            )}
                            <span className="spc-control-title">{label}</span>
                        </div>
                    )}
                    <div className="spc-controls-content">
                        {control !== 'individual' && (
                            // <RangeControl
                            // 	value={ ( measurement ? measurement[ 0 ] : '' ) }
                            // 	onChange={ ( value ) => onChange( [ value, value, value, value ] ) }
                            // 	min={ min }
                            // 	max={ max }
                            // 	step={ step }
                            // />
                            <div className="spc-full-range-wrapper">
                                <RangeControl
                                    className='spc-range-control-wrapper'
                                    value={(measurement ? measurement[0] : '')}
                                    onChange={(value) => onChange([value, value, value, value])}
                                    min={min}
                                    max={max}
                                    step={step}
                                />
                            </div>

                        )}
                        {control === 'individual' && (
                            <Fragment>
                                <MeasurementSingleControl
                                    placement="top"
                                    label={__('Top', 'shop-cred')}
                                    measurement={(measurement ? measurement[0] : '')}
                                    onChange={(value) => onChange([(value ? Number(value) : value), (measurement && undefined !== measurement[1] && '' !== measurement[1] ? measurement[1] : zero), (measurement && undefined !== measurement[2] && '' !== measurement[2] ? measurement[2] : zero), (measurement && undefined !== measurement[3] && '' !== measurement[3] ? measurement[3] : zero)])}
                                    min={min}
                                    max={max}
                                    step={step}
                                    icon={measureIcons.first}
                                    unit={unit}
                                    allowEmpty={allowEmpty}
                                    preset={preset}
                                />
                                <MeasurementSingleControl
                                    placement="right"
                                    label={__('Right', 'shop-cred')}
                                    measurement={(measurement ? measurement[1] : '')}
                                    onChange={(value) => onChange([(measurement && undefined !== measurement[0] && '' !== measurement[0] ? measurement[0] : zero), (value ? Number(value) : value), (measurement && undefined !== measurement[2] && '' !== measurement[2] ? measurement[2] : zero), (measurement && undefined !== measurement[3] && '' !== measurement[3] ? measurement[3] : zero)])}
                                    min={min}
                                    max={max}
                                    step={step}
                                    icon={measureIcons.second}
                                    unit={unit}
                                    allowEmpty={allowEmpty}
                                    preset={preset}
                                />
                                <MeasurementSingleControl
                                    placement="bottom"
                                    label={__('Bottom', 'shop-cred')}
                                    measurement={(measurement ? measurement[2] : '')}
                                    onChange={(value) => onChange([(measurement && undefined !== measurement[0] && '' !== measurement[0] ? measurement[0] : zero), (measurement && undefined !== measurement[1] && '' !== measurement[1] ? measurement[1] : zero), (value ? Number(value) : value), (measurement && undefined !== measurement[3] && '' !== measurement[3] ? measurement[3] : zero)])}
                                    min={min}
                                    max={max}
                                    step={step}
                                    icon={measureIcons.third}
                                    unit={unit}
                                    allowEmpty={allowEmpty}
                                    preset={preset}
                                />
                                <MeasurementSingleControl
                                    placement="left"
                                    label={__('Left', 'shop-cred')}
                                    measurement={(measurement ? measurement[3] : '')}
                                    onChange={(value) => onChange([(measurement && undefined !== measurement[0] && '' !== measurement[0] ? measurement[0] : zero), (measurement && undefined !== measurement[1] && '' !== measurement[1] ? measurement[1] : zero), (measurement && undefined !== measurement[2] && '' !== measurement[2] ? measurement[2] : zero), (value ? Number(value) : value)])}
                                    min={min}
                                    max={max}
                                    step={step}
                                    icon={measureIcons.fourth}
                                    unit={unit}
                                    allowEmpty={allowEmpty}
                                    preset={preset}
                                />
                            </Fragment>
                        )}
                        {(onUnit || showUnit) && (
                            <div className="spc-units">
                                {units.length === 1 ? (
                                    <Button
                                        className="is-active is-single"
                                        isSmall
                                        disabled
                                    >{('%' === unit ? percentIcon : getUnitIcon(unit))}</Button>
                                ) : (
                                    <DropdownMenu
                                        icon={('%' === unit ? percentIcon : getUnitIcon(unit))}
                                        label={__('Select a Unit', 'shop-cred')}
                                        controls={units.map((singleUnit) => createLevelControlToolbar(singleUnit))}
                                        className={'spc-units-group'}
                                        popoverProps={POPOVER_PROPS}
                                    />
                                )}
                            </div>
                        )}
                        {onControl && (
                            <div className="spc-units spc-locked">
                                {control !== 'individual' ? (
                                    <Tooltip text={__('Individual', 'shop-cred')}>
                                        <Button
                                            className="is-single spc-unit-btn-linked"
                                            isSmall
                                            onClick={() => onControl('individual')}
                                            icon={icons.notLinke}
                                        >
                                            {measureIcons.link}
                                        </Button>
                                    </Tooltip>
                                ) : (
                                    <Tooltip text={__('Linked', 'shop-cred')}>
                                        <Button
                                            className="is-single spc-unit-btn-not-linked"
                                            isSmall
                                            onClick={() => onControl('linked')}
                                            icon={icons.link}
                                        >
                                            {measureIcons.unlink}
                                        </Button>
                                    </Tooltip>
                                )}
                            </div>
                        )}
                    </div>
                </div>
            )}
        </>
    );
}