import { PanelBody } from '@wordpress/components';
import { useRef } from '@wordpress/element';
import React from 'react';
import getSPCEditorStateLocalStorage from '../../utils/hooks/getSPCEditorStateLocalStorage';
import { select } from '@wordpress/data';

const SPCAdvancedPanelBody = (props) => {

	const {
		children
	} = props;

	const panelRef = useRef(null);
	const spcSettingState = getSPCEditorStateLocalStorage('spcSettingState');

	const onPanelToggle = () => {

		if ('enabled' === spc_block_object.collapse_panels) {
			const siblings = getSiblings(panelRef.current);

			siblings.forEach((element) => {
				element.querySelector('.components-button').click();
			});
		}

		// Below code is to set the setting state of Tab for each block.
		const { getSelectedBlock } = select('core/block-editor');
		const blockName = getSelectedBlock()?.name;

		let match = false;
		panelRef?.current?.classList.forEach(
			function (value) {
				if (value.includes('spc-advance-panel-body')) {
					match = value
				}
			}
		);
		let inspectorTabName = 'style';
		if (panelRef?.current?.parentElement?.classList.contains('spcb-tab-content-general')) {
			inspectorTabName = 'general';
		}
		if (panelRef?.current?.parentElement?.classList.contains('spcb-tab-content-advance')) {
			inspectorTabName = 'advance';
		}

		const data = {
			...spcSettingState,
			[blockName]: {
				...spcSettingState?.[blockName],
				selectedPanel: match,
				selectedTab: inspectorTabName
			}
		}

		const spcLocalStorage = getSPCEditorStateLocalStorage();
		if (spcLocalStorage) {
			spcLocalStorage.setItem('spcSettingState', JSON.stringify(data));
		}
	}

	const getSiblings = function (elem) {

		const siblings = [];
		let sibling = elem.parentNode.firstChild;

		while (sibling) {
			if (sibling.nodeType === 1 && sibling !== elem && sibling.classList.contains('is-opened')) {
				siblings.push(sibling);
			}
			sibling = sibling.nextSibling
		}

		return siblings;

	};

	const panelTitle = props?.title ? props?.title.toLowerCase().replace(/[^a-zA-Z ]/g, '').replace(/\s+/g, '-') : '';

	return (
		<PanelBody
			{...props}
			onToggle={onPanelToggle}
			ref={panelRef}
			className={`spc-advance-panel-body-${panelTitle}`}
		>
			{children}
		</PanelBody>
	);
}
export default React.memo(SPCAdvancedPanelBody);
