/**
 * BLOCK: Post Excerpt
 */

// Import block dependencies and components
import { ProductExcerpt } from "./edit"
import save from "./save"
import attributes from "./attributes"
//  Import CSS.
import "./style.scss"
// Components
import { __ } from '@wordpress/i18n';
import icons from "../../../utils/icons";

// Register block controls
const { registerBlockType } = wp.blocks

// Register the block
registerBlockType("spc/product-excerpt", {
	title: 'Product Excerpt',
	description: 'Product Excerpt',
	icon: icons.postGrid,
	category: 'shop-cred',
	parent: ["spc/woo-product"],
	keywords: [
		__("Product"),
		__("excerpt"),
		__("spc"),
	],
	ProductExcerpt,
	attributes,
	example: {},
	save
})
