/**
 * BLOCK: Post Image
 */

// Import block dependencies and components
import { ProductImage } from "./edit"
import save from "./save"
import "./editor.scss"
import "./style.scss"
// Components
import { __ } from '@wordpress/i18n';
import icons from "../../../utils/icons";

// Register block controls
const { registerBlockType } = wp.blocks

// Register the block
registerBlockType("spc/product-image", {
	title: 'Product Image',
	description: 'Product Image',
	icon: icons.postGrid,
	category: 'shop-cred',
	parent: ["spc/woo-product"],
	keywords: [
		__("Product"),
		__("image"),
		__("Thumb"),
	],
	ProductImage,
	example: {},
	save
})
