import ResponsiveControl from "../../components/responsive-control"

import SPCAdvancedPanelBody from "../../components/advanced-panel-body"

import {
    TextControl,
    ToggleControl,
    PanelRow,
    RangeControl,
    SelectControl,
} from '@wordpress/components';

import { __ } from '@wordpress/i18n';

const globalSwiperController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        displayLayoutType,
        productCarouselEffect,
        productCarouselAutoplay,
        productCarouselDelay,
        productCarouselLoop,
        productCarouselSpeed,
        productCarouselPauseOnHover,
        productCarouselSlidesPerView,
        productCarouselSlidesPerViewTablet,
        productCarouselSlidesPerViewMobile,
        productCarouselSlidesPerColumnEnable,
        productCarouselSlidesPerColumn,
        productCarouselSlidesGridEnable,
        productCarouselSlidesGridNumber,
        productCarouselSpaceBetween,
        productCarouselGrabCursor,
        productCarouselObserver,
        productCarouselObserveParents,
        productCarouselBreakpoints,
        productCarouselPagination,
        productCarouselPaginationType,
        productCarouselNavigation,
        productCarouselNavigationNextEl,
        productCarouselNavigationPrevEl,
        productCarouselProgressBar,

    } = attributes;


    return (

        <>
            {"carousel" === displayLayoutType &&
                <SPCAdvancedPanelBody title={__("Carousel Settings", 'shop-cred')} initialOpen={false}>
                    <PanelRow className="spc-relative-row">
                        <ResponsiveControl className="spc-responsive-control" />
                        {"Desktop" === deviceType && (
                            <div className="spc-full-range-wrapper">
                                <RangeControl
                                    className='spc-range-control-wrapper'
                                    label={__("Columns")}
                                    value={productCarouselSlidesPerView}
                                    onChange={(value) => setAttributes({ productCarouselSlidesPerView: value })}
                                    min={1}
                                    max={6}
                                />
                            </div>)
                        }
                        {"Tablet" === deviceType && (
                            <div className="spc-full-range-wrapper">
                                <RangeControl
                                    className='spc-range-control-wrapper'
                                    label={__("Columns")}
                                    value={productCarouselSlidesPerViewTablet}
                                    onChange={(value) => setAttributes({ productCarouselSlidesPerViewTablet: value })}
                                    min={1}
                                    max={3}
                                /></div>)
                        }
                        {"Mobile" === deviceType && (
                            <div className="spc-full-range-wrapper">
                                <RangeControl
                                    className='spc-range-control-wrapper'
                                    label={__("Columns")}
                                    value={productCarouselSlidesPerViewMobile}
                                    onChange={(value) => setAttributes({ productCarouselSlidesPerViewMobile: value })}
                                    min={1}
                                    max={6}
                                /></div>)
                        }
                    </PanelRow>
                    <div className="spc-full-range-wrapper">
                        <RangeControl
                            className='spc-range-control-wrapper'
                            label={__("Space Between")}
                            value={productCarouselSpaceBetween}
                            onChange={(value) => setAttributes({ productCarouselSpaceBetween: value })}
                            min={0}
                            max={50}
                        />
                    </div>

                    <ToggleControl
                        label={__('Enable Clomns Slider', 'shop-cred')}
                        checked={productCarouselSlidesPerColumnEnable}
                        onChange={() => {
                            setAttributes({ productCarouselSlidesPerColumnEnable: !productCarouselSlidesPerColumnEnable });
                        }}
                    />
                    {!productCarouselSlidesPerColumnEnable && (
                        <div className="spc-full-range-wrapper">
                            <RangeControl
                                className='spc-range-control-wrapper'
                                label={__("Slider Columns")}
                                value={productCarouselSlidesPerColumn}
                                onChange={(value) => setAttributes({ productCarouselSlidesPerColumn: value })}
                                min={0}
                                max={6}
                            />
                        </div>)}

                    <ToggleControl
                        label={__('Enable Autoplay', 'shop-cred')}
                        checked={productCarouselAutoplay}
                        onChange={() => {
                            setAttributes({ productCarouselAutoplay: !productCarouselAutoplay });
                        }}
                    />

                    <TextControl
                        label={__('Speed', 'shop-cred')}
                        type={'number'}
                        value={productCarouselSpeed}
                        min={0}
                        onChange={productCarouselSpeed => setAttributes({ productCarouselSpeed })}
                    />

                    <TextControl
                        label={__('Delay', 'shop-cred')}
                        type={'number'}
                        value={productCarouselDelay}
                        min={0}
                        onChange={productCarouselDelay => setAttributes({ productCarouselDelay })}
                    />

                    <ToggleControl
                        label={__('Enable Loop', 'shop-cred')}
                        checked={productCarouselLoop}
                        onChange={() => {
                            setAttributes({ productCarouselLoop: !productCarouselLoop });
                        }}
                    />

                    <ToggleControl
                        label={__('Grab Cursor', 'shop-cred')}
                        checked={productCarouselGrabCursor}
                        onChange={() => {
                            setAttributes({ productCarouselGrabCursor: !productCarouselGrabCursor });
                        }}
                    />

                    <ToggleControl
                        label={__('Observer', 'shop-cred')}
                        checked={productCarouselObserver}
                        onChange={() => {
                            setAttributes({ productCarouselObserver: !productCarouselObserver });
                        }}
                    />

                    <ToggleControl
                        label={__('Observe Parents', 'shop-cred')}
                        checked={productCarouselObserveParents}
                        onChange={() => {
                            setAttributes({ productCarouselObserveParents: !productCarouselObserveParents });
                        }}
                    />

                    <ToggleControl
                        label={__('Enable Pagination', 'shop-cred')}
                        checked={productCarouselPagination}
                        onChange={() => {
                            setAttributes({ productCarouselPagination: !productCarouselPagination });
                        }}
                    />

                    {productCarouselPagination && (

                        <SelectControl
                            label={__("Pagination Type", 'shop-cred')}
                            className="spc-select-control-wrapper"
                            value={productCarouselPaginationType}
                            options={[
                                { value: "", label: __("None", 'shop-cred') },
                                { value: "bullets", label: __("Bullets", 'shop-cred') },
                            ]}
                            onChange={value => {
                                setAttributes({ productCarouselPaginationType: value })
                            }}
                        />

                    )}
                    <ToggleControl
                        label={__('Enable Navigation', 'shop-cred')}
                        checked={productCarouselNavigation}
                        onChange={() => {
                            setAttributes({ productCarouselNavigation: !productCarouselNavigation });
                        }}
                    />



                </SPCAdvancedPanelBody>
            }
        </>
    )
}

export default globalSwiperController