import SPCAdvancedPanelBody from "../../components/advanced-panel-body"
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'
import TypographyControl from "../../components/typography"
import {
    PanelRow,
    RangeControl,
    SelectControl
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalTaxonomyController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // Taxonomy style
        displayProductTaxonomy,
        productTaxonomyColor,
        productTaxonomyFontSize,
        productTaxonomyFontSizeType,
        productTaxonomyFontSizeTablet,
        productTaxonomyFontSizeMobile,
        productTaxonomyFontFamily,
        productTaxonomyFontWeight,
        productTaxonomyFontSubset,
        productTaxonomyLineHeight,
        productTaxonomyLineHeightType,
        productTaxonomyLineHeightTablet,
        productTaxonomyLineHeightMobile,
        productTaxonomyLoadGoogleFonts,
        productTaxonomyTextTransform,
        productTaxonomyLetterSpacing,
        productTaxonomyMargin,
        productTaxonomyMarginUnit,
        productTaxonomyBGColor,
        productTaxonomyBGHColor,
        productTaxonomyPadding,
        productTaxonomyPaddingUnit,
        productTaxonomyBR,
        productTaxonomyBRUnit,
        productTaxonomyBorderStyleNormal,
        productTaxonomyBorderWidthNormal,
        productTaxonomyBorderColorNormal,
        productTaxonomyBorderStyleHover,
        productTaxonomyBorderWidthHover,
        productTaxonomyBorderColorHover,

    } = attributes;

    //padding start

    const [productTaxonomyPaddingControl, setproductTaxonomyPaddingControl] = useState('individual');

    const productTaxonomyPaddingMin = (productTaxonomyPaddingUnit === 'em' || productTaxonomyPaddingUnit === 'rem' ? -12 : -200);
    const productTaxonomyPaddingMax = (productTaxonomyPaddingUnit === 'em' || productTaxonomyPaddingUnit === 'rem' ? 24 : 200);
    const productTaxonomyPaddingStep = (productTaxonomyPaddingUnit === 'em' || productTaxonomyPaddingUnit === 'rem' ? 0.1 : 1);

    const saveproductTaxonomyPadding = (value) => {
        const newUpdate = productTaxonomyPadding.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productTaxonomyPadding: newUpdate,
        });
    };

    //padding end

    //Margin start

    const [productTaxonomyMarginControl, setproductTaxonomyMarginControl] = useState('individual');

    const productTaxonomyMarginMin = (productTaxonomyMarginUnit === 'em' || productTaxonomyMarginUnit === 'rem' ? -12 : -200);
    const productTaxonomyMarginMax = (productTaxonomyMarginUnit === 'em' || productTaxonomyMarginUnit === 'rem' ? 24 : 200);
    const productTaxonomyMarginStep = (productTaxonomyMarginUnit === 'em' || productTaxonomyMarginUnit === 'rem' ? 0.1 : 1);

    const saveproductTaxonomyMargin = (value) => {
        const newUpdate = productTaxonomyMargin.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productTaxonomyMargin: newUpdate,
        });
    };
    //Margin End

    //Border Radius start

    const [productTaxonomyBRControl, setproductTaxonomyBRControl] = useState('individual');

    const productTaxonomyBRMin = (productTaxonomyBRUnit === 'em' || productTaxonomyBRUnit === 'rem' ? -12 : -200);
    const productTaxonomyBRMax = (productTaxonomyBRUnit === 'em' || productTaxonomyBRUnit === 'rem' ? 24 : 200);
    const productTaxonomyBRStep = (productTaxonomyBRUnit === 'em' || productTaxonomyBRUnit === 'rem' ? 0.1 : 1);

    const saveproductTaxonomyBR = (value) => {
        const newUpdate = productTaxonomyBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            productTaxonomyBR: newUpdate,
        });
    };

    //Border Radius end

    return (

        <>

            {displayProductTaxonomy &&
                <SPCAdvancedPanelBody title={__("Taxonomy", 'shop-cred')} initialOpen={false}>

                    <Fragment>
                        <TypographyControl
                            label={__("Typography", 'shop-cred')}
                            attributes={attributes}
                            setAttributes={setAttributes}
                            loadGoogleFonts={{ value: productTaxonomyLoadGoogleFonts, label: "productTaxonomyLoadGoogleFonts" }}
                            fontFamily={{ value: productTaxonomyFontFamily, label: "productTaxonomyFontFamily" }}
                            fontWeight={{ value: productTaxonomyFontWeight, label: "productTaxonomyFontWeight" }}
                            fontSubset={{ value: productTaxonomyFontSubset, label: "productTaxonomyFontSubset" }}
                            fontSizeType={{ value: productTaxonomyFontSizeType, label: "productTaxonomyFontSizeType" }}
                            fontSize={{ value: productTaxonomyFontSize, label: "productTaxonomyFontSize" }}
                            fontSizeMobile={{ value: productTaxonomyFontSizeMobile, label: "productTaxonomyFontSizeMobile" }}
                            fontSizeTablet={{ value: productTaxonomyFontSizeTablet, label: "productTaxonomyFontSizeTablet" }}
                            lineHeightType={{ value: productTaxonomyLineHeightType, label: "productTaxonomyLineHeightType" }}
                            lineHeight={{ value: productTaxonomyLineHeight, label: "productTaxonomyLineHeight" }}
                            lineHeightMobile={{ value: productTaxonomyLineHeightMobile, label: "productTaxonomyLineHeightMobile" }}
                            lineHeightTablet={{ value: productTaxonomyLineHeightTablet, label: "productTaxonomyLineHeightTablet" }}
                            textTransform={{ value: productTaxonomyTextTransform, label: 'productTaxonomyTextTransform' }}
                            letterSpacing={{ value: productTaxonomyLetterSpacing, label: 'productTaxonomyLetterSpacing' }}
                        />
                    </Fragment>

                    <h2>{__("Color", 'shop-cred')}</h2>

                    <Fragment>
                        <div className="spc-color-component-settings">
                            <p className="spc-color-component-custom-text">
                                {__("Color", 'shop-cred')}
                            </p>
                            <ColorPalette
                                className="spc-color-component-custom-class"
                                value={productTaxonomyColor}
                                onChange={(colorValue) => setAttributes({ productTaxonomyColor: colorValue })}
                                allowReset
                                enableAlpha
                            />
                        </div>
                    </Fragment>


                    <div className="spc-color-component-settings">
                        <p className="spc-color-component-custom-text">
                            {__("Background Color", 'shop-cred')}
                        </p>
                        <ColorPalette
                            className="spc-color-component-custom-class"
                            value={productTaxonomyBGColor}
                            onChange={(colorValue) => setAttributes({ productTaxonomyBGColor: colorValue })}
                            enableAlpha
                        />
                    </div>

                    <PanelRow>
                        <SelectControl
                            label={__("Border Style", 'shop-cred')}
                            className="spc-select-control-wrapper"
                            value={productTaxonomyBorderStyleNormal}
                            options={[
                                { value: "none", label: __("None", 'shop-cred') },
                                { value: "solid", label: __("Solid", 'shop-cred') },
                                { value: "dotted", label: __("Dotted", 'shop-cred') },
                                { value: "dashed", label: __("Dashed", 'shop-cred') },
                                { value: "double", label: __("Double", 'shop-cred') },
                            ]}
                            onChange={value => {
                                setAttributes({ productTaxonomyBorderStyleNormal: value })
                            }}
                        />
                    </PanelRow>
                    {"none" !== productTaxonomyBorderStyleNormal &&
                        <PanelRow>
                            <div className="spc-full-range-wrapper">
                                <RangeControl
                                    className='spc-range-control-wrapper'
                                    label={__("Border Width (px)", 'shop-cred')}
                                    value={productTaxonomyBorderWidthNormal}
                                    onChange={value => {
                                        setAttributes({ productTaxonomyBorderWidthNormal: value })
                                    }}
                                    min={0}
                                    max={20}
                                    allowReset
                                />
                            </div>
                        </PanelRow>
                    }
                    {"none" !== productTaxonomyBorderStyleNormal &&
                        <>

                            <div className="spc-color-component-settings">
                                <p className="spc-color-component-custom-text">
                                    {__("Border Color", 'shop-cred')}
                                </p>
                                <ColorPalette
                                    className="spc-color-component-custom-class"
                                    value={productTaxonomyBorderColorNormal}
                                    onChange={(value) => setAttributes({ productTaxonomyBorderColorNormal: value })}
                                    allowReset
                                    enableAlpha
                                />
                            </div>
                        </>
                    }

                    <PanelRow className="spc-relative-row">
                        <ResponsiveMeasurementControls
                            label={__('Padding', 'shop-cred')}
                            value={undefined !== productTaxonomyPadding && undefined !== productTaxonomyPadding[0] && undefined !== productTaxonomyPadding[0].desk ? productTaxonomyPadding[0].desk : ['20', '20', '20', '20']}
                            control={productTaxonomyPaddingControl}
                            tabletValue={undefined !== productTaxonomyPadding && undefined !== productTaxonomyPadding[0] && undefined !== productTaxonomyPadding[0].tablet ? productTaxonomyPadding[0].tablet : ['10', '10', '10', '10']}
                            mobileValue={undefined !== productTaxonomyPadding && undefined !== productTaxonomyPadding[0] && undefined !== productTaxonomyPadding[0].mobile ? productTaxonomyPadding[0].mobile : ['10', '10', '10', '10']}
                            onChange={(value) => saveproductTaxonomyPadding({ desk: value })}
                            onChangeTablet={(value) => saveproductTaxonomyPadding({ tablet: value })}
                            onChangeMobile={(value) => saveproductTaxonomyPadding({ mobile: value })}
                            onChangeControl={(value) => setproductTaxonomyPaddingControl(value)}
                            onChangeTabletControl={(value) => setproductTaxonomyPaddingControl(value)}
                            onChangeMobileControl={(value) => setproductTaxonomyPaddingControl(value)}
                            min={productTaxonomyPaddingMin}
                            max={productTaxonomyPaddingMax}
                            step={productTaxonomyPaddingStep}
                            unit={productTaxonomyPaddingUnit}
                            units={['px', 'em', '%']}
                            onUnit={(value) => setAttributes({ productTaxonomyPaddingUnit: value })}
                        />
                    </PanelRow>

                    <PanelRow className="spc-relative-row">
                        <ResponsiveMeasurementControls
                            label={__('Border Radius', 'shop-cred')}
                            value={undefined !== productTaxonomyBR && undefined !== productTaxonomyBR[0] && undefined !== productTaxonomyBR[0].desk ? productTaxonomyBR[0].desk : ['20', '20', '20', '20']}
                            control={productTaxonomyBRControl}
                            tabletValue={undefined !== productTaxonomyBR && undefined !== productTaxonomyBR[0] && undefined !== productTaxonomyBR[0].tablet ? productTaxonomyBR[0].tablet : ['10', '10', '10', '10']}
                            mobileValue={undefined !== productTaxonomyBR && undefined !== productTaxonomyBR[0] && undefined !== productTaxonomyBR[0].mobile ? productTaxonomyBR[0].mobile : ['10', '10', '10', '10']}
                            onChange={(value) => saveproductTaxonomyBR({ desk: value })}
                            onChangeTablet={(value) => saveproductTaxonomyBR({ tablet: value })}
                            onChangeMobile={(value) => saveproductTaxonomyBR({ mobile: value })}
                            onChangeControl={(value) => setproductTaxonomyBRControl(value)}
                            onChangeTabletControl={(value) => setproductTaxonomyBRControl(value)}
                            onChangeMobileControl={(value) => setproductTaxonomyBRControl(value)}
                            min={productTaxonomyBRMin}
                            max={productTaxonomyBRMax}
                            step={productTaxonomyBRStep}
                            unit={productTaxonomyBRUnit}
                            units={['px', 'em', '%']}
                            onUnit={(value) => setAttributes({ productTaxonomyBRUnit: value })}
                        />
                    </PanelRow>


                    <hr className="spc-editor-separator" />

                    <PanelRow className="spc-relative-row">
                        <ResponsiveMeasurementControls
                            label={__('Margin', 'shop-cred')}
                            value={undefined !== productTaxonomyMargin && undefined !== productTaxonomyMargin[0] && undefined !== productTaxonomyMargin[0].desk ? productTaxonomyMargin[0].desk : ['20', '20', '20', '20']}
                            control={productTaxonomyMarginControl}
                            tabletValue={undefined !== productTaxonomyMargin && undefined !== productTaxonomyMargin[0] && undefined !== productTaxonomyMargin[0].tablet ? productTaxonomyMargin[0].tablet : ['10', '10', '10', '10']}
                            mobileValue={undefined !== productTaxonomyMargin && undefined !== productTaxonomyMargin[0] && undefined !== productTaxonomyMargin[0].mobile ? productTaxonomyMargin[0].mobile : ['10', '10', '10', '10']}
                            onChange={(value) => saveproductTaxonomyMargin({ desk: value })}
                            onChangeTablet={(value) => saveproductTaxonomyMargin({ tablet: value })}
                            onChangeMobile={(value) => saveproductTaxonomyMargin({ mobile: value })}
                            onChangeControl={(value) => setproductTaxonomyMarginControl(value)}
                            onChangeTabletControl={(value) => setproductTaxonomyMarginControl(value)}
                            onChangeMobileControl={(value) => setproductTaxonomyMarginControl(value)}
                            min={productTaxonomyMarginMin}
                            max={productTaxonomyMarginMax}
                            step={productTaxonomyMarginStep}
                            unit={productTaxonomyMarginUnit}
                            units={['px', 'em', '%',]}
                            onUnit={(value) => setAttributes({ productTaxonomyMarginUnit: value })}
                        />

                    </PanelRow>

                </SPCAdvancedPanelBody>
            }

        </>
    )
}

export default GlobalTaxonomyController