import './editor.scss';
import React, { useLayoutEffect } from 'react';
import classnames from 'classnames';
import { __ } from '@wordpress/i18n';
import {
	cloneElement,
	Children,
	useState,
	useRef,
	useEffect,
} from '@wordpress/element';
import { select } from '@wordpress/data';
import getSPCEditorStateLocalStorage from '../../utils/hooks/getSPCEditorStateLocalStorage';

const LAYOUT = 'general',
	STYLE = 'style',
	ADVANCE = 'advance';

const InspectorTabs = (props) => {
	// Add and remove the CSS on the drop and remove of the component.
	useLayoutEffect(() => {
		// styles.use();
		// return () => {
		// 	styles.unuse();
		// };
	}, []);

	const spcSettingState = getSPCEditorStateLocalStorage('spcSettingState');

	const { defaultTab, children, tabs } = props;
	const [currentTab, setCurrentTab] = useState(defaultTab ? defaultTab : tabs[0]);

	const tabContainer = useRef();

	let sidebarPanel;

	useEffect(() => {
		sidebarPanel = tabContainer.current.closest('.components-panel');
	});

	const renderSPCTabsSettingsInOrder = () => {

		// Inspector Tabs Priority Rendering Code. (Conflicts with 3rd Party plugin panels in Inspector Panel)
		const tabsContainer = document.querySelector('.spcb-inspector-tabs-container');
		let tabsGeneralContainer = document.querySelector('.spcb-tab-content-general');
		let tabsStyleContainer = document.querySelector('.spcb-tab-content-style');
		let tabsAdvanceContainer = document.querySelector('.spcb-tab-content-advance');

		if (tabsContainer) {
			const tabsParent = tabsContainer.parentElement;

			if (tabsParent) {
				tabsGeneralContainer = tabsGeneralContainer ? tabsGeneralContainer : '';
				tabsStyleContainer = tabsStyleContainer ? tabsStyleContainer : '';
				tabsAdvanceContainer = tabsAdvanceContainer ? tabsAdvanceContainer : '';
				tabsParent.prepend(tabsContainer, tabsGeneralContainer, tabsStyleContainer, tabsAdvanceContainer);
			}
		}
	};

	// component did mount
	useEffect(() => {

		renderSPCTabsSettingsInOrder();

		const { getSelectedBlock } = select('core/block-editor');
		const blockName = getSelectedBlock()?.name;
		// This code is to fix the side-effect of the editor responsive click settings panel refresh issue.
		if (spcSettingState && spcSettingState[blockName] && currentTab !== spcSettingState[blockName]?.selectedTab) {
			setCurrentTab(spcSettingState[blockName]?.selectedTab || 'general')
			if (sidebarPanel) {
				sidebarPanel.setAttribute('data-spcb-tab', spcSettingState[blockName]?.selectedTab || 'general');
			}
		} else if (sidebarPanel) {
			sidebarPanel.setAttribute('data-spcb-tab', 'general');
		}
		// Above Section Ends.
		// component will unmount
		return () => {

			if (sidebarPanel) {
				const inspectorTabs = sidebarPanel.querySelector(
					'.spcb-inspector-tabs-container'
				);

				if (!inspectorTabs || null === inspectorTabs) {
					sidebarPanel.removeAttribute('data-spcb-tab');
				}
			}
		};

	}, []);

	const _onTabChange = (tab) => {
		renderSPCTabsSettingsInOrder();
		setCurrentTab(tab);

		if (sidebarPanel) {
			sidebarPanel.setAttribute('data-spcb-tab', tab);
		}

		// Below code is to set the setting state of Tab for each block.
		const { getSelectedBlock } = select('core/block-editor');
		const blockName = getSelectedBlock()?.name;

		const data = {
			...spcSettingState,
			[blockName]: {
				selectedTab: tab
			}
		}
		const spcLocalStorage = getSPCEditorStateLocalStorage();
		if (spcLocalStorage) {
			spcLocalStorage.setItem('spcSettingState', JSON.stringify(data));
		}
	};

	return (
		<>
			<div className={'spcb-inspector-tabs-container'}>
				{ /*
				 * The tabs is static, you must use layout, style & advance
				 */ }
				<div
					ref={tabContainer}
					className={classnames(
						'spcb-inspector-tabs',
						'spcb-inspector-tabs-count-' + tabs.length,
						currentTab
					)}
				>
					{tabs.indexOf(LAYOUT) > -1 && (
						<button
							className={classnames({
								'spcb-active': currentTab === LAYOUT,
							})}
							onClick={() => _onTabChange(LAYOUT)}
						>
							<h5>{__('General')}</h5>
						</button>
					)}

					{tabs.indexOf(STYLE) > -1 && (
						<button
							className={classnames({
								'spcb-active': currentTab === STYLE,
							})}
							onClick={() => _onTabChange(STYLE)}
						>
							<h5>{__('Style')}</h5>
						</button>
					)}

					{tabs.indexOf(ADVANCE) > -1 && (
						<button
							className={classnames({
								'spcb-active': currentTab === ADVANCE,
							})}
							onClick={() => _onTabChange(ADVANCE)}
						>
							<h5>{__('Advanced')}</h5>
						</button>
					)}
				</div>
			</div>

			{Array.isArray(children) &&
				Children.map(children, (child, index) => {
					if (!child.key) {
						throw new Error(
							'props.key not found in <InspectorTab />, you must use `key` prop'
						);
					}
					return cloneElement(child, {
						index,
						isActive: child.key === currentTab,
					});
				})}
		</>
	);
};

InspectorTabs.defaultProps = {
	defaultTab: 'general',
	tabs: ['general', 'style', 'advance'],
};

export default InspectorTabs;
