/**
 * Responsive Range Component
 *
 */

/**
 * Internal block libraries
 */
import "./editor.scss"

import { useSelect, useDispatch } from '@wordpress/data';
import { useState, useRef } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { map, isEqual } from 'lodash';
import MeasurementControls from '../measurement-control';
import { undo } from '@wordpress/icons';
import {
    Button,
    ButtonGroup,
} from '@wordpress/components';

import icons from '../../../utils/icons';

/**
 * Build the Measure controls
 * @returns {object} Measure settings.
 */
export default function ResponsiveMeasurementControls({
    label,
    subLabel,
    onChange,
    onChangeTablet,
    onChangeMobile,
    mobileValue,
    tabletValue,
    value,
    onChangeMobileControl,
    mobileControl,
    onChangeTabletControl,
    tabletControl,
    onChangeControl,
    control,
    step = 1,
    max = 100,
    min = 0,
    unit = '',
    onUnit,
    showUnit = false,
    units = ['px', 'em', 'rem'],
    allowEmpty = true,
    preset = '',
    hasToggle = true,
    isBorderRadius = false,
    firstIcon = '',
    secondIcon = '',
    thirdIcon = '',
    fourthIcon = '',
    linkIcon = '',
    unlinkIcon = '',
    reset = true,
}) {
    const ref = useRef();
    const [localControl, setLocalControl] = useState('individual');
    const realDesktopControl = control ? control : localControl;
    const realMobileControl = mobileControl ? mobileControl : realDesktopControl;
    const realTabletControl = tabletControl ? tabletControl : realDesktopControl;
    const realOnChangeDesktopControl = onChangeControl ? onChangeControl : setLocalControl;
    const realOnChangeTabletControl = onChangeTabletControl ? onChangeTabletControl : realOnChangeDesktopControl;
    const realOnChangeMobileControl = onChangeMobileControl ? onChangeMobileControl : realOnChangeDesktopControl;
    const zero = (allowEmpty ? true : false);
    const [deviceType, setDeviceType] = useState('Desktop');
    const theDevice = useSelect((select) => {
        return select('core/edit-post').__experimentalGetPreviewDeviceType();
    }, []);
    if (theDevice !== deviceType) {
        setDeviceType(theDevice);
    }
    const {
        __experimentalSetPreviewDeviceType: setPreviewDeviceType,
    } = useDispatch('core/edit-post');
    const customSetPreviewDeviceType = (device) => {
        setPreviewDeviceType(device);
        setDeviceType(device);
    };

    const devices = [
        {
            name: 'Desktop',
            title: icons.desktop,
            itemClass: 'spc-desk-tab',
        },
        {
            name: 'Tablet',
            title: icons.tablet,
            itemClass: 'spc-tablet-tab',
        },
        {
            name: 'Mobile',
            key: 'mobile',
            title: icons.mobile,
            itemClass: 'spc-mobile-tab',
        },
    ];
    let liveValue = (value ? value : ['', '', '', '']);
    if (deviceType === 'Tablet') {
        liveValue = (tabletValue ? tabletValue : ['', '', '', '']);
    } else if (deviceType === 'Mobile') {
        liveValue = (mobileValue ? mobileValue : ['', '', '', '']);
    }
    const onReset = () => {
        if (deviceType === 'Tablet') {
            onChangeTablet(['', '', '', '']);
        } else if (deviceType === 'Mobile') {
            onChangeMobile(['', '', '', '']);
        } else {
            onChange(['', '', '', '']);
        }
    }
    const output = {};
    output.Mobile = (
        <MeasurementControls
            key={2}
            className="measure-mobile-size"
            label={(subLabel ? __('Mobile:', 'shop-cred') + subLabel : undefined)}
            measurement={(mobileValue ? mobileValue : ['', '', '', ''])}
            control={(realMobileControl ? realMobileControl : 'individual')}
            onChange={(size) => onChangeMobile(size)}
            onControl={(hasToggle ? (sizeControl) => realOnChangeMobileControl(sizeControl) : undefined)}
            min={min}
            max={max}
            step={step}
            allowEmpty={zero}
            unit={unit}
            showUnit={true}
            units={[unit]}
            preset={preset}
            isBorderRadius={isBorderRadius}
            firstIcon={firstIcon}
            secondIcon={secondIcon}
            thirdIcon={thirdIcon}
            fourthIcon={fourthIcon}
            linkIcon={linkIcon}
            unlinkIcon={unlinkIcon}
        />
    );
    output.Tablet = (
        <MeasurementControls
            key={1}
            className="measure-tablet-size"
            label={(subLabel ? __('Tablet:', 'shop-cred') + subLabel : undefined)}
            measurement={(tabletValue ? tabletValue : ['', '', '', ''])}
            control={(realTabletControl ? realTabletControl : 'individual')}
            onChange={(size) => onChangeTablet(size)}
            onControl={(hasToggle ? (sizeControl) => realOnChangeTabletControl(sizeControl) : undefined)}
            min={min}
            max={max}
            step={step}
            allowEmpty={zero}
            unit={unit}
            showUnit={true}
            units={[unit]}
            preset={preset}
            isBorderRadius={isBorderRadius}
            firstIcon={firstIcon}
            secondIcon={secondIcon}
            thirdIcon={thirdIcon}
            fourthIcon={fourthIcon}
            linkIcon={linkIcon}
            unlinkIcon={unlinkIcon}
        />
    );
    output.Desktop = (
        <MeasurementControls
            key={0}
            className="measure-desktop-size"
            label={(subLabel ? subLabel : undefined)}
            measurement={(value ? value : ['', '', '', ''])}
            control={(realDesktopControl ? realDesktopControl : 'individual')}
            onChange={(size) => onChange(size)}
            onControl={(hasToggle ? (sizeControl) => realOnChangeDesktopControl(sizeControl) : undefined)}
            min={min}
            max={max}
            step={step}
            allowEmpty={zero}
            unit={unit}
            onUnit={(onUnit ? onUnit : undefined)}
            showUnit={showUnit}
            units={units}
            preset={preset}
            isBorderRadius={isBorderRadius}
            firstIcon={firstIcon}
            secondIcon={secondIcon}
            thirdIcon={thirdIcon}
            fourthIcon={fourthIcon}
            linkIcon={linkIcon}
            unlinkIcon={unlinkIcon}
        />
    );
    return [
        onChange && onChangeTablet && onChangeMobile && (
            <div ref={ref} className={'components-base-control spc-responsive-measure-control'}>
                <div className="spc-title-bar">
                    {label && (
                        <span className="spc-control-title">{label}</span>
                    )}
                    <ButtonGroup className="spc-measure-responsive-options" aria-label={__('Device', 'shop-cred')}>
                        {map(devices, ({ name, key, title, itemClass }) => (
                            <Button
                                key={key}
                                className={`spc-responsive-btn ${itemClass}${name === deviceType ? ' is-active' : ''}`}
                                isSmall
                                aria-pressed={deviceType === name}
                                onClick={() => customSetPreviewDeviceType(name)}
                            >
                                {title}
                            </Button>
                        ))}
                    </ButtonGroup>
                    {reset && (
                        <Button
                            className="is-reset is-single spc-size-btn spc-reset-btn"
                            isSmall
                            disabled={((isEqual(['', '', '', ''], liveValue) || isEqual(['', 'auto', '', 'auto'], liveValue)) ? true : false)}

                            onClick={() => onReset()}
                        >{__('Reset')}</Button>
                    )}
                </div>
                <div className="spc-responsive-measure-control-inner">
                    {(output[deviceType] ? output[deviceType] : output.Desktop)}
                </div>
            </div>
        ),
    ];
}