/**
 * BLOCK: Post Button
 */

// Import block dependencies and components
import { CartButton } from "./edit"
import save from "./save"
import "./editor.scss"
// Components
import { __ } from '@wordpress/i18n';
import icons from "../../../utils/icons";

// Register block controls
const { registerBlockType } = wp.blocks

// Register the block
registerBlockType("spc/cart-button", {
	title: 'Cart Button',
	description: 'Cart Button',
	icon: icons.postGrid,
	category: 'shop-cred',
	parent: ["spc/woo-product"],
	keywords: [
		__("product"),
		__("cart button"),
		__("button"),
		__("spc"),
	],
	CartButton,
	example: {},
	save
})
