const { dateI18n, format, __experimentalGetSettings } = wp.date

import { __ } from '@wordpress/i18n';

export const ProductTags = (props) => {

	const { post, attributes } = props

	const dateFormat = __experimentalGetSettings().formats.date

	let sale_Text = (attributes.saleText) ? attributes.saleText : __("Sale")

	let stock_Text = post.spc_product_stock_status
	let product_discount = post.spc_product_discount

	var hot_Text = ''

	if (post.spc_product_featured) {
		hot_Text = (attributes.hotText) ? attributes.hotText : __("Hot")
	}

	if (attributes.displaySaleTag || attributes.displayfeaturedTag || attributes.displayStockTag) {
		return (
			<>

				{attributes.displaySaleTag && product_discount &&
					<span dangerouslySetInnerHTML={{ __html: product_discount }} className='spc-product-tag spc-discount-price' />
				}

				{attributes.displayfeaturedTag && post.spc_product_featured &&
					<span className='spc-product-tag spc-featured-product'>
						<span className='spc-hot'>{hot_Text}</span>
					</span>

				}

				{attributes.displayStockTag && stock_Text &&
					<span dangerouslySetInnerHTML={{ __html: stock_Text }} className='spc-product-tag spc-stock-status' />
				}

			</>
		)
	} else {
		return null
	}

}

