
import SPCAdvancedPanelBody from "../../components/advanced-panel-body"
import TypographyControl from "../../components/typography"
import ResponsiveMeasurementControls from '../../components/measurement/responsive-measurement-control'
import BoxShadowControl from '../../components/box-shadow-control'

import {
    TextControl,
    PanelRow,
    RangeControl,
    TabPanel,
    SelectControl,
    Button
} from '@wordpress/components';

import {
    useState,
    Fragment
} from '@wordpress/element';

import {
    ColorPalette,
} from '@wordpress/block-editor';

import { __ } from '@wordpress/i18n';

const GlobalQuickViewController = (props) => {

    const { attributes, block_id, deviceType, setAttributes } = props

    const {
        // QuickView
        displayQuickView,
        gQuickViewText,
        gQuickViewColor,
        gQuickViewHColor,
        gQuickViewFontSize,
        gQuickViewFontSizeType,
        gQuickViewFontSizeTablet,
        gQuickViewFontSizeMobile,
        gQuickViewFontFamily,
        gQuickViewFontWeight,
        gQuickViewTextTransform,
        gQuickViewLetterSpacing,
        gQuickViewFontSubset,
        gQuickViewLineHeight,
        gQuickViewLineHeightType,
        gQuickViewLineHeightTablet,
        gQuickViewLineHeightMobile,
        gQuickViewLoadGoogleFonts,
        gQuickViewAlignment,
        gQuickViewPosTop,
        gQuickViewPosBottom,
        gQuickViewPosLeft,
        gQuickViewPosRight,
        gQuickViewBGColor,
        gQuickViewBGHColor,
        gQuickViewSpacing,
        gQuickViewPadding,
        gQuickViewPaddingUnit,
        gQuickViewBR,
        gQuickViewBRUnit,
        gQuickViewBorderStyleNormal,
        gQuickViewBorderWidthNormal,
        gQuickViewBorderColorNormal,
        gQuickViewBorderStyleHover,
        gQuickViewBorderWidthHover,
        gQuickViewBorderColorHover,
        gQuickViewBoxShadowColor,
        gQuickViewBoxShadowHOffset,
        gQuickViewBoxShadowVOffset,
        gQuickViewBoxShadowBlur,
        gQuickViewBoxShadowSpread,
        gQuickViewBoxShadowPosition,
        gQuickViewBoxShadowColorHover,
        gQuickViewBoxShadowHOffsetHover,
        gQuickViewBoxShadowVOffsetHover,
        gQuickViewBoxShadowBlurHover,
        gQuickViewBoxShadowSpreadHover,
        gQuickViewBoxShadowPositionHover,

    } = attributes;


    //padding start

    const [gQuickViewPaddingControl, setgQuickViewPaddingControl] = useState('individual');

    const gQuickViewPaddingMin = (gQuickViewPaddingUnit === 'em' || gQuickViewPaddingUnit === 'rem' ? -12 : -200);
    const gQuickViewPaddingMax = (gQuickViewPaddingUnit === 'em' || gQuickViewPaddingUnit === 'rem' ? 24 : 200);
    const gQuickViewPaddingStep = (gQuickViewPaddingUnit === 'em' || gQuickViewPaddingUnit === 'rem' ? 0.1 : 1);

    const savegQuickViewPadding = (value) => {
        const newUpdate = gQuickViewPadding.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gQuickViewPadding: newUpdate,
        });
    };

    //Border Radius start

    const [gQuickViewBRControl, setgQuickViewBRControl] = useState('individual');

    const gQuickViewBRMin = (gQuickViewBRUnit === 'em' || gQuickViewBRUnit === 'rem' ? -12 : -200);
    const gQuickViewBRMax = (gQuickViewBRUnit === 'em' || gQuickViewBRUnit === 'rem' ? 24 : 200);
    const gQuickViewBRStep = (gQuickViewBRUnit === 'em' || gQuickViewBRUnit === 'rem' ? 0.1 : 1);

    const savegQuickViewBR = (value) => {
        const newUpdate = gQuickViewBR.map((item, index) => {
            if (0 === index) {
                item = { ...item, ...value };
            }
            return item;
        });
        setAttributes({
            gQuickViewBR: newUpdate,
        });
    };

    //Border Radius end

    return (

        <>
            {displayQuickView &&
                <SPCAdvancedPanelBody title={__("QuickView", 'shop-cred')} initialOpen={false}>

                    <TextControl
                        label={__("Quick View Text", 'shop-cred')}
                        value={gQuickViewText}
                        placeholder={__('Quick View', 'shop-cred')}
                        onChange={value => setAttributes({ gQuickViewText: value })}
                    />

                    <div className="spc-editor-alignment-group-wrapper">
                        <p> {__("Alignment", 'shop-cred')}</p>
                        <div className="spc-editor-alignment-group">
                            <Button
                                className={'spc-editor-alignleft'}
                                key={"left"}
                                icon="editor-alignleft"
                                label="Left"
                                onClick={() => setAttributes({ gQuickViewAlignment: "start" })}
                                aria-pressed={"left" === gQuickViewAlignment}
                                isPrimary={"left" === gQuickViewAlignment}
                            />
                            <Button
                                className={'spc-editor-aligncenter'}
                                key={"center"}
                                icon="editor-aligncenter"
                                label="Center"
                                onClick={() => setAttributes({ gQuickViewAlignment: "center" })}
                                aria-pressed={"center" === gQuickViewAlignment}
                                isPrimary={"center" === gQuickViewAlignment}
                            />
                            <Button
                                className={'spc-editor-alignright'}
                                key={"end"}
                                icon="editor-alignright"
                                label="Right"
                                onClick={() => setAttributes({ gQuickViewAlignment: "end" })}
                                aria-pressed={"end" === gQuickViewAlignment}
                                isPrimary={"end" === gQuickViewAlignment}
                            />
                        </div>
                    </div>

                    <h2>{__("Typography", 'shop-cred')}</h2>
                    <TypographyControl
                        label={__("Typography", 'shop-cred')}
                        attributes={attributes}
                        setAttributes={setAttributes}
                        loadGoogleFonts={{ value: gQuickViewLoadGoogleFonts, label: "gQuickViewLoadGoogleFonts" }}
                        fontFamily={{ value: gQuickViewFontFamily, label: "gQuickViewFontFamily" }}
                        fontWeight={{ value: gQuickViewFontWeight, label: "gQuickViewFontWeight" }}
                        fontSubset={{ value: gQuickViewFontSubset, label: "gQuickViewFontSubset" }}
                        fontSizeType={{ value: gQuickViewFontSizeType, label: "gQuickViewFontSizeType" }}
                        fontSize={{ value: gQuickViewFontSize, label: "gQuickViewFontSize" }}
                        fontSizeMobile={{ value: gQuickViewFontSizeMobile, label: "gQuickViewFontSizeMobile" }}
                        fontSizeTablet={{ value: gQuickViewFontSizeTablet, label: "gQuickViewFontSizeTablet" }}
                        lineHeightType={{ value: gQuickViewLineHeightType, label: "gQuickViewLineHeightType" }}
                        lineHeight={{ value: gQuickViewLineHeight, label: "gQuickViewLineHeight" }}
                        lineHeightMobile={{ value: gQuickViewLineHeightMobile, label: "gQuickViewLineHeightMobile" }}
                        lineHeightTablet={{ value: gQuickViewLineHeightTablet, label: "gQuickViewLineHeightTablet" }}
                        textTransform={{ value: gQuickViewTextTransform, label: 'gQuickViewTextTransform' }}
                        letterSpacing={{ value: gQuickViewLetterSpacing, label: 'gQuickViewLetterSpacing' }}
                    />

                    <PanelRow className="spc-relative-row">

                        <ResponsiveMeasurementControls
                            label={__('Padding', 'shop-cred')}
                            value={undefined !== gQuickViewPadding && undefined !== gQuickViewPadding[0] && undefined !== gQuickViewPadding[0].desk ? gQuickViewPadding[0].desk : ['0', '0', '0', '0']}
                            control={gQuickViewPaddingControl}
                            tabletValue={undefined !== gQuickViewPadding && undefined !== gQuickViewPadding[0] && undefined !== gQuickViewPadding[0].tablet ? gQuickViewPadding[0].tablet : ['0', '0', '0', '0']}
                            mobileValue={undefined !== gQuickViewPadding && undefined !== gQuickViewPadding[0] && undefined !== gQuickViewPadding[0].mobile ? gQuickViewPadding[0].mobile : ['0', '0', '0', '0']}
                            onChange={(value) => savegQuickViewPadding({ desk: value })}
                            onChangeTablet={(value) => savegQuickViewPadding({ tablet: value })}
                            onChangeMobile={(value) => savegQuickViewPadding({ mobile: value })}
                            onChangeControl={(value) => setgQuickViewPaddingControl(value)}
                            onChangeTabletControl={(value) => setgQuickViewPaddingControl(value)}
                            onChangeMobileControl={(value) => setgQuickViewPaddingControl(value)}
                            min={gQuickViewPaddingMin}
                            max={gQuickViewPaddingMax}
                            step={gQuickViewPaddingStep}
                            unit={gQuickViewPaddingUnit}
                            units={['px', 'em', '%']}
                            onUnit={(value) => setAttributes({ gQuickViewPaddingUnit: value })}
                        />

                    </PanelRow>

                    <PanelRow className="spc-relative-row">
                        <TabPanel className="spc-inspect-tabs spc-inspect-tabs-col-2"
                            activeClass="active-tab"
                            tabs={[
                                {
                                    name: "normal",
                                    title: __("Normal", 'shop-cred'),
                                    className: "spc-normal-tab spc-tabs-control",
                                },
                                {
                                    name: "hover",
                                    title: __("Hover", 'shop-cred'),
                                    className: "spc-focus-tab spc-tabs-control",
                                },
                            ]}>
                            {
                                (tabName) => {
                                    let tabout_color
                                    if ("normal" === tabName.name) {
                                        tabout_color = <Fragment>

                                            <>

                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Normal Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gQuickViewColor}
                                                        onChange={(colorValue) => setAttributes({ gQuickViewColor: colorValue })}
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>

                                            <>

                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Background Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gQuickViewBGColor}
                                                        onChange={(colorValue) => setAttributes({ gQuickViewBGColor: colorValue })}
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                            <PanelRow>
                                                <SelectControl
                                                    label={__("Border Style", 'shop-cred')}
                                                    className="spc-select-control-wrapper"
                                                    value={gQuickViewBorderStyleNormal}
                                                    options={[
                                                        { value: "none", label: __("None", 'shop-cred') },
                                                        { value: "solid", label: __("Solid", 'shop-cred') },
                                                        { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                        { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                        { value: "double", label: __("Double", 'shop-cred') },
                                                    ]}
                                                    onChange={value => {
                                                        setAttributes({ gQuickViewBorderStyleNormal: value })
                                                    }}
                                                />
                                            </PanelRow>
                                            {"none" !== gQuickViewBorderStyleNormal &&
                                                <PanelRow>
                                                    <div className="spc-full-range-wrapper">
                                                        <RangeControl
                                                            className='spc-range-control-wrapper'
                                                            label={__("Border Width (px)", 'shop-cred')}
                                                            value={gQuickViewBorderWidthNormal}
                                                            onChange={value => {
                                                                setAttributes({ gQuickViewBorderWidthNormal: value })
                                                            }}
                                                            min={0}
                                                            max={20}
                                                            allowReset
                                                        />
                                                    </div>
                                                </PanelRow>
                                            }
                                            {"none" !== gQuickViewBorderStyleNormal &&
                                                <>
                                                    <hr className="spc-editor-separator" />
                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Border Color", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gQuickViewBorderColorNormal}
                                                            onChange={(value) => setAttributes({ gQuickViewBorderColorNormal: value })}
                                                            allowReset
                                                            enableAlpha
                                                        />
                                                    </div>
                                                </>
                                            }
                                            <PanelRow className="spc-relative-row">
                                                <ResponsiveMeasurementControls
                                                    label={__('Border Radius', 'shop-cred')}
                                                    value={undefined !== gQuickViewBR && undefined !== gQuickViewBR[0] && undefined !== gQuickViewBR[0].desk ? gQuickViewBR[0].desk : ['20', '20', '20', '20']}
                                                    control={gQuickViewBRControl}
                                                    tabletValue={undefined !== gQuickViewBR && undefined !== gQuickViewBR[0] && undefined !== gQuickViewBR[0].tablet ? gQuickViewBR[0].tablet : ['10', '10', '10', '10']}
                                                    mobileValue={undefined !== gQuickViewBR && undefined !== gQuickViewBR[0] && undefined !== gQuickViewBR[0].mobile ? gQuickViewBR[0].mobile : ['10', '10', '10', '10']}
                                                    onChange={(value) => savegQuickViewBR({ desk: value })}
                                                    onChangeTablet={(value) => savegQuickViewBR({ tablet: value })}
                                                    onChangeMobile={(value) => savegQuickViewBR({ mobile: value })}
                                                    onChangeControl={(value) => setgQuickViewBRControl(value)}
                                                    onChangeTabletControl={(value) => setgQuickViewBRControl(value)}
                                                    onChangeMobileControl={(value) => setgQuickViewBRControl(value)}
                                                    min={gQuickViewBRMin}
                                                    max={gQuickViewBRMax}
                                                    step={gQuickViewBRStep}
                                                    unit={gQuickViewBRUnit}
                                                    units={['px', 'em', '%']}
                                                    onUnit={(value) => setAttributes({ gQuickViewBRUnit: value })}
                                                />
                                            </PanelRow>

                                            <PanelRow>
                                                <BoxShadowControl {...props}
                                                    attributes={attributes}
                                                    setAttributes={setAttributes}
                                                    label={__("Box Shadow", 'shop-cred')}
                                                    boxShadowColor={'gQuickViewShadowColor'}
                                                    boxShadowHOffset={'gQuickViewShadowHOffset'}
                                                    boxShadowVOffset={'gQuickViewShadowVOffset'}
                                                    boxShadowBlur={'gQuickViewShadowBlur'}
                                                    boxShadowSpread={'gQuickViewShadowSpread'}
                                                    boxShadowPosition={'gQuickViewShadowPosition'}
                                                />
                                            </PanelRow>
                                        </Fragment>
                                    } else {
                                        tabout_color = <Fragment>
                                            <>
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Active Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gQuickViewHColor}
                                                        onChange={(colorValue) => setAttributes({ gQuickViewHColor: colorValue })}
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>

                                            <>
                                                <div className="spc-color-component-settings">
                                                    <p className="spc-color-component-custom-text">
                                                        {__("Background Color", 'shop-cred')}
                                                    </p>
                                                    <ColorPalette
                                                        className="spc-color-component-custom-class"
                                                        value={gQuickViewBGHColor}
                                                        onChange={(colorValue) => setAttributes({ gQuickViewBGHColor: colorValue })}
                                                        enableAlpha
                                                    />
                                                </div>
                                            </>
                                            <PanelRow>
                                                <SelectControl
                                                    label={__("Border Style", 'shop-cred')}
                                                    className="spc-select-control-wrapper"
                                                    value={gQuickViewBorderStyleHover}
                                                    options={[
                                                        { value: "none", label: __("None", 'shop-cred') },
                                                        { value: "solid", label: __("Solid", 'shop-cred') },
                                                        { value: "dotted", label: __("Dotted", 'shop-cred') },
                                                        { value: "dashed", label: __("Dashed", 'shop-cred') },
                                                        { value: "double", label: __("Double", 'shop-cred') },
                                                    ]}
                                                    onChange={value => {
                                                        setAttributes({ gQuickViewBorderStyleHover: value })
                                                    }}
                                                />
                                            </PanelRow>
                                            {"none" !== gQuickViewBorderStyleHover &&
                                                <PanelRow>
                                                    <div className="spc-full-range-wrapper">
                                                        <RangeControl
                                                            className='spc-range-control-wrapper'
                                                            label={__("Border Width (px)", 'shop-cred')}
                                                            value={gQuickViewBorderWidthHover}
                                                            onChange={value => {
                                                                setAttributes({ gQuickViewBorderWidthHover: value })
                                                            }}
                                                            min={0}
                                                            max={20}
                                                            allowReset
                                                        />
                                                    </div>
                                                </PanelRow>
                                            }
                                            {"none" !== gQuickViewBorderStyleHover &&
                                                <>
                                                    <hr className="spc-editor-separator" />
                                                    <div className="spc-color-component-settings">
                                                        <p className="spc-color-component-custom-text">
                                                            {__("Border Color", 'shop-cred')}
                                                        </p>
                                                        <ColorPalette
                                                            className="spc-color-component-custom-class"
                                                            value={gQuickViewBorderColorHover}
                                                            onChange={(value) => setAttributes({ gQuickViewBorderColorHover: value })}
                                                            allowReset
                                                            enableAlpha
                                                        />
                                                    </div>
                                                </>
                                            }
                                            <PanelRow>
                                                <BoxShadowControl {...props}
                                                    attributes={attributes}
                                                    setAttributes={setAttributes}
                                                    label={__("Box Shadow", 'shop-cred')}
                                                    boxShadowColor={'gQuickViewShadowColorHover'}
                                                    boxShadowHOffset={'gQuickViewShadowHOffsetHover'}
                                                    boxShadowVOffset={'gQuickViewShadowVOffsetHover'}
                                                    boxShadowBlur={'gQuickViewShadowBlurHover'}
                                                    boxShadowSpread={'gQuickViewShadowSpreadHover'}
                                                    boxShadowPosition={'gQuickViewShadowPositionHover'}
                                                />
                                            </PanelRow>
                                        </Fragment>
                                    }
                                    return <div>{tabout_color}</div>
                                }
                            }
                        </TabPanel>
                    </PanelRow>
                </SPCAdvancedPanelBody>
            }
        </>
    )
}

export default GlobalQuickViewController