// import Swiper core and required modules
import { Navigation, Pagination, Scrollbar, A11y, Autoplay } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import "swiper/css/effect-cards";
import "swiper/css/grid";

import classnames from "classnames"
import {
	InnerBlockLayoutContextProvider,
	renderPostLayout
} from '.././function';

import { __ } from '@wordpress/i18n';

const Product = (props) => {

	const {
		attributes,
		className,
		latestProduct,
		block_id,
		categoriesList,
		deviceType,
		pGridSlideShow,
		tpGridSlideShow,
		mpGridSlideShow,
	} = props

	const {
		columns,
		tcolumns,
		mcolumns,
		postsToShow,
		equalHeight,
		displayProductPagination,
		productPaginationMarkup,
		productPagination,
		layoutConfig,
		PcontentOrder,
		displayLayoutType,
		loadMoreProduct,
		loadMoreProductMarkup,
		//carousel
		productCarouselEffect,
		productCarouselAutoplay,
		productCarouselDelay,
		productCarouselLoop,
		productCarouselSpeed,
		productCarouselPauseOnHover,
		productCarouselSlidesPerView,
		productCarouselSlidesPerViewTablet,
		productCarouselSlidesPerViewMobile,
		productCarouselSlidesPerColumnEnable,
		productCarouselSlidesPerColumn,
		productCarouselSlidesGridEnable,
		productCarouselSlidesGridNumber,
		productCarouselSpaceBetween,
		productCarouselGrabCursor,
		productCarouselObserver,
		productCarouselObserveParents,
		productCarouselBreakpoints,
		productCarouselPagination,
		productCarouselPaginationType,
		productCarouselNavigation,
		productCarouselNavigationNextEl,
		productCarouselNavigationPrevEl,
		productCarouselProgressBar,
	} = attributes


	const equalHeightClass = equalHeight ? "spc-woo-product-equal-height" : ""

	let setDeviceType = deviceType.toLowerCase()

	let setBreakPoints
	if (setDeviceType === "desktop") {
		setBreakPoints = {
			320: {
				slidesPerView: mpGridSlideShow,
			},
			640: {
				width: 640,
				slidesPerView: tpGridSlideShow,

			},
			1024: {
				slidesPerView: pGridSlideShow,
			},
		}
	} else if (setDeviceType === "tablet") {

		setBreakPoints = {
			640: {
				width: 640,
				slidesPerView: tpGridSlideShow,
			},

		}
	} else {
		setBreakPoints = {
			320: {
				width: 320,
				slidesPerView: mpGridSlideShow,
			},
		}
	}


	const displayProducts = latestProduct.length > postsToShow ?
		latestProduct.slice(0, postsToShow) :
		latestProduct;

	return (
		<div key={`spc-block-${block_id}`}
			className={classnames(
				className,
				`spc-woo-product-wrapper`,
				`spc-editor-preview-mode-${deviceType.toLowerCase()}`,
				`spc-block-${block_id}`
			)} id={`spc-block-${block_id}`}
		>

			{"carousel" === attributes.displayLayoutType &&

				<Swiper
					// install Swiper modules
					modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
					effect={attributes.productCarouselEffect}
					delay={attributes.productCarouselAutoplay ? true : false}
					autoplay={attributes.productCarouselAutoplay}
					speed={attributes.productCarouselAutoplay ? attributes.productCarouselSpeed : null}
					slidesPerView={pGridSlideShow ? pGridSlideShow : mpGridSlideShow}
					breakpoints={setBreakPoints}
					spaceBetween={attributes.productCarouselSpaceBetween}
					slidesPerColumn={attributes.productCarouselSlidesPerColumn}
					loop={attributes.productCarouselLoop}
					grabCursor={attributes.productCarouselGrabCursor}
					navigation={attributes.productCarouselNavigation ? { nextEl: '.spc-carousel-nav-next', prevEl: '.spc-carousel-nav-prev' } : false}
					pagination={attributes.productCarouselPagination ? { clickable: true, type: attributes.productCarouselPaginationType, el: '.spc-swiper-pagination' } : false}
				>
					{displayProducts.map((post = {}, i) =>
						<SwiperSlide>
							<InnerBlockLayoutContextProvider
								parentName="spc/woo-product"
								parentClassName="spc-block-grid"
							>

								<article key={i} >
									<div className="spc-woo-product-inner-wrap" >

										{renderPostLayout(
											"spc/woo-product",
											post,
											layoutConfig,
											PcontentOrder,
											props.attributes,
											props.categoriesList
										)}

									</div>
								</article>

							</InnerBlockLayoutContextProvider>
						</SwiperSlide>
					)}

				</Swiper>

			}
			{
				"grid" === attributes.displayLayoutType &&
				<div
					className={classnames(
						"is-grid",
						`spc-woo-product-columns-${columns}`,
						`spc-woo-product-columns-tablet-${tcolumns}`,
						`spc-woo-product-columns-mobile-${mcolumns}`,
						"spc-woo-product-items",
						`${equalHeightClass}`
					)}
				>
					<InnerBlockLayoutContextProvider
						parentName="spc/woo-product"
						parentClassName="spc-block-grid"
					>
						{displayProducts.map((post = {}, i) =>
							<article key={i} className={post.spc_get_class_name} >
								<div className="spc-woo-product-inner-wrap" >

									{renderPostLayout(
										"spc/woo-product",
										post,
										layoutConfig,
										PcontentOrder,
										props.attributes,
										props.categoriesList
									)}

								</div>
							</article>
						)}
					</InnerBlockLayoutContextProvider>

				</div>

			}

			{
				displayProductPagination == true && 'empty' !== productPaginationMarkup && 'grid' === displayLayoutType &&
				<div dangerouslySetInnerHTML={{ __html: productPaginationMarkup }} className="spc-product-pagination-wrap"></div>
			}
			{
				"grid" === attributes.displayLayoutType && displayProductPagination == false && loadMoreProduct &&
				<div className="spc-woo-product-load-btn">
					<a className="spc-woo-product-loadmore-btn" href="#">{__("Load More", 'shop-cred')}</a>
				</div>
			}
			{
				"carousel" === attributes.displayLayoutType && attributes.productCarouselNavigation &&
				<div className="spc-nav-wrapper">
					<div className="spc-carousel-nav-next"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-chevron-right"><polyline points="9 18 15 12 9 6"></polyline></svg></div>
					<div className="spc-carousel-nav-prev"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-chevron-left"><polyline points="15 18 9 12 15 6"></polyline></svg></div>
				</div>
			}
			{
				"carousel" === attributes.displayLayoutType && attributes.productCarouselPagination &&
				<div className="spc-dots-container">
					<div className="spc-swiper-pagination swiper-pagination"></div>
				</div>
			}
		</div >

	)

}

export default Product
